.dashboard_container{
    display: flex;    
    margin-top: 20px;
    .dashboard_links {
        width: 20%;        
        background-color: $primary-color;        
        .profile_info{
            text-align: center;
            padding: 30px;
            h5{
                color: $primary-color-yellow;
                font-size: 1.4rem;
                font-weight: lighter;
                margin-top: 10px;
            }
            .profile_progressbar{
                background-color: $color-light-grey;
                height: 10px;
                border-radius: 10px;
                margin-top: 10px;
                position: relative;
                span{
                    display: inline-block;
                    background-color: $primary-color-yellow;
                    width: 20%;
                    position: absolute;
                    left: 0;
                    height: 100%; 
                    border-radius: 10px;
                    margin-top: 0;                   
                }
            }
            span{
                display: block;
                text-align: center;
                font-size: 1.2rem;
                margin-top: 10px;
                color: $primary-color-yellow;
            }
        }

        .profile_links{
            margin-top: 20px;
            ul{
                li{   
                    margin-bottom: 20px;
                    a{
                        color: $color-light-grey;
                        font-size: 1.6rem;
                        display: inline-block;
                        display: flex;
                        align-items: center;
                        transition: 0.3s; 
                        position: relative;
                        padding: 10px 0;
                        padding-left: 30px;
                        svg{
                            width: 18px;
                            margin-right: 10px;
                        }
                        &:hover, &.active{
                            color: $primary-color-yellow;
                            &::before{
                                content: "";
                                position: absolute;
                                left: 0;
                                height: 100%;
                                width: 10px;
                                border-top-right-radius: 8px;
                                border-bottom-right-radius: 8px;
                                background-color: $primary-color-yellow;                                
                            }
                            svg{
                                path{
                                    fill: $primary-color-yellow;
                                }                                
                            }
                        }                        
                    }
                }
                &.logout_btn{
                    margin-top: 100px;
                }
            }
        }
    }

    .dashboard_section{
        width: 80%;
        background-color: $color-light-grey;
        padding: 30px;    
        .dashboard_sec{
            h2{
                font-size: 2.5rem;
                color: $primary-color-yellow;
                font-weight: lighter;
            }

            .user_profile_pic{
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-top: 30px;

                .profile_pic{
                    display: flex;
                    align-items: center;
                    img{
                        width: 70px;
                    }
                    a{
                        font-size: 1.4rem;
                        display: inline-block;
                        margin-left: 15px;
                        transition: 0.3s;
                        &:hover{
                            color: $primary-color-yellow;
                        }
                    }
                }

                span{
                    font-size: 1.2rem;
                    font-family: $font-italic;
                }
            }

            ul{
                display: flex;
                align-items: flex-end;
                justify-content: space-between;
                flex-wrap: wrap;
                width: 90%;
                li{
                    margin-bottom: 50px;
                    p{                            
                        margin-bottom: 15px; 
                        font-size: 1.4rem;                           
                    }
                    &.user_name{
                        span{
                            display: block;
                            font-size: 1.4rem;                          
                        }
                    }                    

                    &.user_dob{
                        select{
                            margin-right: 15px;
                            width: auto;
                            &:last-child{
                                margin-right: 0;
                            }                        
                        }
                    }
                    
                    &.user_password{
                        .password_sec{
                            display: flex;
                            align-items: center;
                            margin-bottom: 10px;
                            input{
                                background-color: transparent;
                            }
                            a{
                                width: 20px;
                                img{
                                    width: 100%;
                                }
                            }                        
                        }
                        a{
                            font-size: 1.4rem;                            
                            text-decoration: underline;
                            transition: 0.3s;
                            &:hover{
                                color: $primary-color-yellow;
                            }
                        }
                    } 
                    
                    &.user_interested{
                        position: relative;
                        span{
                            display: block;
                            position: absolute;
                            bottom: -20px;
                            left: 0;
                        }
                    }
                }
                &.col-3{
                    li{
                        width: 30%;
                    }
                }
                &.col-6{
                    li{
                        width: 48%;
                    }
                }
            }

            .user_info{
                margin-top: 30px;            
            }

            .submit_btn{
                input{
                    padding: 14px 30px 10px;
                }
            }            

            &.myprofile{
                .bid_limit_box{
                    background-color: $color-white;                
                    margin-top: 50px;
                    padding: 20px;
                    border-radius: 10px;
                    border: 1px $color-grey solid;
                    width: 50%;
                    h5{
                        font-size: 1.4rem;
                        position: relative;
                        display: inline-block;
                        a{
                            position: absolute;
                            right: -20px;
                            top: -15px;
                            background-color: $primary-color-yellow; 
                            font-size: 1.2rem;
                            border-radius: 50%;                    
                            font-weight: bold;
                            width: 18px;
                            height: 18px;
                            line-height: 0;   
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            .tooltiptext {
                                visibility: hidden;
                                width: 120px;
                                background-color: $primary-color-yellow;
                                color: $color-white;
                                text-align: center;
                                border-radius: 6px;
                                padding: 15px 0;                                        
                                position: absolute;
                                z-index: 1;
                                top: -35px;
                                left: 50%;
                                margin-left: -60px;
                            }
                            &:hover{
                                .tooltiptext{
                                    visibility: visible;
                                }                    
                            }
                        }                
                    }
                    ul{
                        margin-top: 20px;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        li{
                            margin-bottom: 0;
                            span{
                                display: block;
                                font-size: 1.2rem;
                            }
                            h4{
                                font-size: 2rem;
                                margin-top: 10px;
                            }
                        }
                    }
                }
            }
        }
    }

    .postal_sec, .biling_sec, .bank_sec{
        margin-top: 50px;
        padding-top: 50px;
        border-top: 1px $primary-color solid;
        h3{
            font-size: 1.8rem;
        }

        form{
            margin-top: 30px;
            .contry_sec{
                display: flex;
                justify-content: space-between;
                select, input{
                    width: 45%;
                }                
            }

            .billing_add{
                margin-top: 30px;
            }

            .file_sec{
                display: flex;
                justify-content: space-between;
                input[type=text]{
                    margin-right: 25px;
                }
            }
        }
    }
}