.global_heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;

  h2{
    display: inline-block;
    margin-bottom: 0;    
    font-size: 3.5rem;
    font-weight: normal;
    position: relative;
    &::before{
      content: "";
      width: 100px;
      height: 1px;
      background-color: $primary-color;
      position: absolute;
      left: -100px;
      bottom: 7px;
    }
    &::after{
      content: "";
      width: 100%;
      height: 1px;
      background-color: $primary-color;
      position: absolute;
      left: 0;
      bottom: 7px;
    }
  }

  .auction_nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 8%;
    .slider_nav{            
      position: static;
      margin-top: 0;
      background-color: $primary-color-yellow;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      padding: 18px;      
      &::after{
        font-size: 12px;
        font-weight: bold;
        color: $primary-color;
      }        
    }
  }  
}

.heading_inner{
  display: flex;
  align-items: center;
  justify-content: space-between;
  .global_heading{
    width: 8%;
    .auction_nav{
      width: 100%;
      margin-right: 0;
    }
  }
  h3{
    font-size: 2.5rem;    
    font-weight: lighter;
  }  
}

.breadcrumb{
  @media (max-width: $breakpoint-mobile-big){
    margin-bottom: 20px;
  }
  ul{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    li{
      font-size: 1.4rem;
      a{
        display: block;
        transition: 0.3s;
        &:link{
          font-weight: bold;
          &::before{
            content: "/";
            font-size: 1.4rem;
          }
          &:hover{
            color: $primary-color-yellow;
            &::before{
              color: $primary-color;
            }
          }
        }          
      }

      &:first-child{
        margin-left: 0;        
      }
    }
  }
}