.auction_container{
    .auction_way{    
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
        position: relative;
    
        .auction_content{
            width: 35%;
            p{
                color: $primary-color;
                font-size: 1.6rem;
                line-height: 25px;
                font-family: $font-regular;
                width: 80%;
                margin-top: 0;
            }

            a{
                display: inline-block;
                background-color: $color-grey;
                color: $primary-color;
                font-family: $font-regular;
                font-size: 1.4rem;
                text-transform: uppercase;
                padding: 12px 30px;
                border-radius: 10px;
                margin-top: 30px;
                transition: 0.3s;
                &:hover{
                    background-color: $primary-color;
                    color: $color-white;
                }
            }
            .global-pagination{
                bottom: -20px;
            }
        }
    
        .auction_slider_container{
            width: 68%;
            overflow: hidden;
            .swiper-wrapper{
                width: 100%;
                height: auto;                
                .item{
                    img{
                        border-radius: 10px;
                        width: 100%;
                    }
                    .auction_info{
                        display: flex;
                        justify-content: space-between;
                        margin-top: 15px;
                        .auction_date{
                            h4{
                                font-size: 2rem;
                                color: $primary-color-yellow;
                                font-weight: normal;
                                margin-top: 0;
                                margin-bottom: 0;
                            }
                            span{
                                display: block;
                                color: $primary-color;
                                font-size: 1.4rem;
                                margin-top: 10px;
                            }
                        }

                        .auction_icon{
                            img{
                                border-radius: 0;
                                width: 25px;
                            }
                        }
                    }
                }            
            }        
        }        
    }
}