@font-face {
    font-family: 'PrataRegular';
    src: url('../../fonts/prata-regular.ttf');
    src: url('../../fonts/prata-regular.woff2') format('woff2'),
        url('../../fonts/prata-regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'LoraRegular';
    src: url('../../fonts/lora-variablefont_wght.ttf');
    src: url('../../fonts/lora-variablefont_wght.woff2') format('woff2'),
        url('../../fonts/lora-variablefont_wght.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'LoraItalic';
    src: url('../../fonts/lora-italic-variablefont_wght.ttf');
    src: url('../../fonts/lora-italic-variablefont_wght.woff2') format('woff2'),
        url('../../fonts/lora-italic-variablefont_wght.woff') format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}

* {
    margin: 0;
    padding: 0;
    list-style: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    border: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
}

html {
    width: 100%;
    height: 100%;
    -webkit-text-size-adjust: 100%;
    font-size: 62.5%;    
    overflow-x: hidden;
}

body {
    font-family: $font-default;
    color: $primary-color;
    height: 100%;    
    max-width: 1920px;
    margin: auto;
}

a, a:hover {
    text-decoration: none;
    outline: none;
    cursor: pointer;
    color: $primary-color;
    font-family: $font-regular;
}

span{
    font-family: $font-regular;
}

img {
    border: none;
}

p, input, textarea {
    font-size: 1.6rem;
    font-family: $font-default;
    outline: none;
    color: $primary-color;
    margin-top: 0;
    margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6{
    margin-top: 0;
    margin-bottom: 0;
}

ul{
    list-style: none;
    margin: 0;
}

table,
table tr td,
table tr th {
    border: none
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

input[type=submit], input[type=button] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;    
    cursor: pointer;   
}

input::-ms-clear,textarea::-ms-clear {
    display: none;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}

select{
    font-family: $font-default;
}

::-moz-selection {
    color: $color-white;
    background: $primary-color-yellow;
}

::selection {
    color: $color-white;
    background: $primary-color-yellow;
}

.mt_70{
    margin-top: 70px;
}

.global_pl{
    padding-left: 30px;
}

.container{
    width: 90%;
    margin: auto;
    max-width: 1450px;
}

.container_inner{
    width: 85%;
    margin: auto;
    max-width: 1920px;
}

.btn{    
    font-size: 1.4rem;
    font-weight: lighter;
    padding: 10px 30px;
    border-radius: 8px;
    display: inline-block;
    text-transform: uppercase;
    transition: 0.3s;
    font-family: "LoraRegular";
    cursor: pointer;
    &:hover{
        background-color: $primary-color;
        color: $color-white;
    }
}

.btn-yellow{
    background-color: $primary-color-yellow;
}

.btn-grey{
    background-color: $color-grey;
}

.btn-transparent{
    background-color: transparent;
    border: 1px $primary-color solid;
    &:hover{
        background-color: $primary-color-yellow;
        border-color: $primary-color-yellow;
        color: $primary-color;
    }
}

label{
    &.error{
        position: absolute;
        left: 0;
        width: 100%;
        bottom: -20px;
        font-size: 1.2rem;
        color: $color-red;
    }
}

.overlay{
    position: fixed;
    top:0;
    left: 0;
    width:100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 1000;
    display: none;
}

.overflow-hide{
    overflow: hidden;
}

.tab-content, .tab-inner-content{
    display: none;
    position: relative;
    z-index: 200 !important;
    &.active{
        display: block;
    }
}

/*-----Custom Radio Buttons-----*/

.custom_radio_cont{
    display: flex;
    .custom_radio {
        display: block;
        position: relative;
        padding-left: 25px; 
        padding-top: 4px;   
        cursor: pointer;
        font-size: 1.4rem;
        font-family: $font-italic;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        margin-right: 40px;
        &:last-child{
            margin-right: 0;
        }
        input{
            position: absolute;
            opacity: 0;
            cursor: pointer;
        }

        .checkmark {
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 20px;
            background-color: transparent;
            border: 1px $primary-color solid;
            border-radius: 50%;
            &:after {
                content: "";
                position: absolute;
                display: none;
                top: 7px;
                left: 7px;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background: $color-white;
            }
        }

        &:hover input ~ .checkmark {
            background-color: transparent;
            border: 1px $primary-color solid;
        }
          
        input:checked ~ .checkmark {
            background-color: $primary-color;
            border: none;
        }

        input:checked ~ .checkmark:after {
            display: block;
        }
    }
}

.custom_img_radio{
    .image_radio{
        text-align: center;
        margin-top: 15px;
        margin-right: 35px;
        cursor: pointer;
        &:last-child{
            margin-right: 0;
        }

        .input-hidden{
            position: absolute;
            left: -9999px;    
        }
    
        input[type=radio] + label{
            cursor: pointer;
        }
        
        input[type=radio] + label > img {        
            width: 100%;                
        }
          
        input[type=radio]:checked + label{
            background-color: $primary-color-yellow;
        }

        span{
            display: block;
            font-size: 1.4rem;
            font-style: italic;
            margin-top: 10px;
        }

        .svg_sec{
            display: flex;                            
            border: 1px $color-grey solid;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            padding: 8px;
            margin: auto;
            margin-top: 0;                            
            transition: 0.3s;                                    
        }
        &:hover, &.active{
            .svg_sec{
                background-color: $primary-color-yellow;
            }
        }
    }    
}

/*-----Custom Radio Buttons-----*/

/*-----Custom Select Buttons-----*/

.custom_select{
    background-color: transparent;
    border-bottom: 1px $primary-color solid;
    font-size: 1.4rem;
    font-family: $font-italic;
    padding-bottom: 5px;
    padding-right: 20px;
    padding-left: 5px;
    appearance: none;
    background: url("../../images/downarrow.svg") no-repeat right;
    background-size: 10px;
    cursor: pointer;
    background-position-y: 2px;
    width: 100%;
    &:focus-visible{
        outline: none;
    }
}

/*-----Custom Select Buttons-----*/

/*-----Custom Input Type-----*/

.custom_input, .password_sec{
    input[type=text], input[type=number], input[type=password]{
        font-size: 1.4rem;
        color: $primary-color;
        font-family: $font-italic;
        border-bottom: 1px $primary-color solid;
        padding-bottom: 10px;
        padding-left: 10px;
        background-color: transparent;
        width: 100%;
        &::placeholder{
            color: $primary-color;
        }
        &:focus{
            &::placeholder{
                color: transparent;
            }
        }
    }
    input[type=password]{
        border-bottom: none;
    }
}

.password_sec{
    border-bottom: 1px $primary-color solid;
}

/*-----Custom Input Type-----*/

/*-----Custom Checkbox-----*/


.custom_check_cont {
    .custom_checkbox{    
        display: block;
        position: relative;
        padding-left: 35px;    
        cursor: pointer;
        font-size: 1.4rem;
        padding-top: 5px;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;    
        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
        }
        .checkmark {
            position: absolute;
            top: 0;
            left: 0;
            height: 20px;
            width: 20px;
            border-radius: 5px;
            background-color: #eee;
            &::after{
                content: "";
                position: absolute;
                display: none;
                left: 7px;
                top: 3px;
                width: 5px;
                height: 8px;
                border: solid white;
                border-width: 0 2px 2px 0;
                -webkit-transform: rotate(35deg);
                -ms-transform: rotate(35deg);
                transform: rotate(35deg);
            }
        }
        &:hover input ~ .checkmark {
            background-color: $color-grey;
        }
        input:checked ~ .checkmark {
            background-color: $primary-color;
        }
        input:checked ~ .checkmark:after {
            display: block;
        }
        &.text_left{
            text-align: left;
        }
    }
}  

  /*-----Custom Checkbox-----*/

/*-----Custom File Upload-----*/

.fileUpload {
    cursor: pointer;
    width: 180px;                    
    overflow: hidden;
    margin-left: 15px;
    border-radius: 5px;
    &::before{
        width: 100%;
        font-size: 1.3rem;
        display: flex;
        align-items: center;
        justify-content: center;
        content: 'Upload New Pic';
        display: inline-block;
        background: $color-grey;
        height: 100%;
        text-align: center;  
        text-transform: uppercase;
        padding: 12px 0 10px;
        border-radius: 5px;
    }
    &::-webkit-file-upload-button {
        visibility: hidden;
    }
}

/*-----Custom File Upload-----*/

.global-pagination{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: left;
    display: flex;
    .swiper-pagination-bullet{
        opacity: 1;
        background-color: $color-dark-grey;
        width: 10px;
        height: 10px;
        cursor: pointer;
        transition: 0.3s;
        &.swiper-pagination-bullet-active{
            background-color: $primary-color-yellow;  
            width: 25px;
            border-radius: 10px;
        }
    }
}

.common_para{
    font-size: 1.4rem;
    line-height: 25px;
    @media (max-width: $breakpoint-mobile-big){
        font-size: 1.5rem;
    }
}

.nicescroll-rails-vr, .nicescroll-rails-hr{
    width: 8px !important;
    border-radius: 10px;
    z-index: 100 !important;
    .nicescroll-cursors{
        width: 100% !important;
    }
}

.common_toggle{
    input[type=checkbox] {
        display: none;
    }

    label {
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        &::before{
            content: "ON";
            position: absolute;
            font-size: 1.2rem;
            left: 10px;
            color: $primary-color;
            font-weight: bold;
            z-index: 10;
            margin-top: 3px;
        }
        .toggle-track {
            display: flex;
            align-items: center;
            height: 35px;
            width: 80px;
            background: $primary-color-yellow;
            border-radius: 20px;
            position: relative;            
            border: 2px solid $primary-color-yellow;
            &::before{
                content: '';
                display: inline-block;
                height: 30px;
                width: 30px;
                background: $color-white;
                border-radius: 50%;
                position: absolute;
                top: 0;
                right: 0;
                transition: right .2s ease-in;
            }
        }
    }

    input[type="checkbox"]:checked + label{
        &::before{        
            content: "OFF";
            right: 10px;
            left: auto;
            color: $color-white;
        }
    }

    input[type="checkbox"]:checked + label .toggle-track{
        background-color: $primary-color;
        border-color: $primary-color;
    }

    input[type="checkbox"]:checked + label .toggle-track:before{
        background: $color-white;
        right: 45px;
    }
} 

.progress_bar{
    background-color: $primary-color;
    height: 7px;
    border-radius: 50px;
    margin-top: 10px;
    position: relative;
    display: block;    
    .progress_inner{
      width: 50%;
      height: 7px;
      border-radius: 50px;
      background-color: $primary-color-yellow;
      position: absolute;
      left: 0;
      height: 100%;
    }
}

.profile_progress{
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 10px;
}

.tooltip{
    display: inline-block;
    width: 20px;
    position: relative;
    img{
        width: 100%;
    }
    .tooltiptext{
        visibility: hidden;
        width: 120px;
        font-size: 1.4rem;
        background-color: $primary-color-yellow;
        color: $primary-color;
        text-align: center;
        padding: 5px 0;
        border-radius: 6px;                                                        
        position: absolute;
        z-index: 1;
        top: -5px;
        left: 125%;                            
        &::after{
            content: "";
            position: absolute;
            top: 50%;
            right: 100%;
            margin-top: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: transparent $primary-color-yellow transparent transparent;
        }
    }
    &:hover{
        .tooltiptext{
            visibility: visible;
        }                        
    }
}

/*-----Multiselect-----*/
  
.multiselect_dropdown {
    margin-top: 20px;
    dd, dt{
        margin: 0px;
        padding: 0px;
    }

    ul{
        margin: -1px 0 0 0;
    }

    dd{
        position: relative;
        ul{
            display: none;
            background-color: $color-white;
            border: 0;
            color: $color-white;
            display: none;
            left: 0px;
            padding: 10px;
            position: absolute;
            top: 0;
            width: 100%;
            list-style: none;
            height: 100px;
            margin-top: 0 !important;
            z-index: 100;
            overflow: auto;
            scrollbar-width: thin;
            scrollbar-color: $primary-color $color-grey;
            border: 1px $primary-color solid;
            &::-webkit-scrollbar {
                width: 6px;
            }   
            &::-webkit-scrollbar-track {
                background-color: $color-grey;                    
                border-radius: 10px;
            }                                
            &::-webkit-scrollbar-thumb {
                background: $primary-color;
                border-radius: 10px;
            }        
            li{
                width: 100% !important;
                margin-bottom: 10px !important;
                color: $primary-color;
                &.custom_check_cont{
                    .custom_checkbox{
                        font-size: 1.2rem;
                        padding-left: 30px;
                        .checkmark{                            
                            border: 1px $primary-color solid;
                        }
                    }
                }            
                a{
                    padding: 5px;
                    display: block;
                    &:hover{
                        background-color: #fff;
                    }
                }
            }
        }
    }

    dt{
        a{            
            display: block;
            overflow: hidden;
            border: 0;
            padding: 0 10px;
            border-bottom: 1px $primary-color solid;   
            background: url("../../images/downarrow.svg") no-repeat right;
            background-size: 10px;
            span{
                position: static !important;
                cursor: pointer;
                display: inline-block;
                font-size: 1.4rem !important;
                padding: 0 3px 2px 0;
            }
        }
    }

    .multiSel {
        margin-top: 0 !important;
        position: absolute;
        left: 0;
        width: 100%;
        bottom: -20px;
        span {
            cursor: pointer;
            display: inline-block;
            padding: 0 3px 2px 0;
            position: static !important;
        }
    }
    span{
        &.value{
            display: none;
        }
    }
}

/*-----Multiselect-----*/