.my_collections{
    background-color: rgba(0,0,0,0.7);    
    padding: 50px 0;
    @media (max-width: $breakpoint-mobile-big){
        padding: 30px 0;
    }
    .container{
        background-color: $color-grey;
        padding: 50px;
        @media (max-width: $breakpoint-tablet){
            width: 90%;            
        }
        @media (max-width: $breakpoint-mobile-big){
            padding: 25px;
        }
        .dash_heading{
            padding-top: 0;
            .container{
                width: 100%;
                padding: 0;
                display: block;
                padding-bottom: 30px;
                border-bottom: 1px $color-dark-grey solid;
                h3{
                    .tooltip{
                        top: -10px;
                    }
                }
                p{
                    margin-top: 30px;
                    @media (max-width: $breakpoint-mobile-big){
                        margin-top: 15px;
                        line-height: 18px;
                    }
                }
                input[type=file]{
                    margin-left: 0;
                    margin-top: 20px;
                    &::before{
                        background-color: $primary-color-yellow;
                        content: "Upload New Art";
                    }
                }
            }
        }

        .album_cont{
            padding-top: 30px;        
            .album_head{
                display: flex;
                align-items: center;
                justify-content: space-between;
                h5{
                    font-size: 1.8rem;
                }
                span{
                    display: inline-block;
                    font-size: 1.4rem;
                    font-weight: bold;
                    font-style: italic;
                }
            }

            ul{
                margin-top: 30px;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                border-bottom: 1px $color-dark-grey solid;
                li{
                    width: 23.5%;
                    background-color: $color-white;
                    padding: 10px 10px 25px;
                    border-radius: 5px;
                    box-shadow: 0px 0px 20px #0000001A;
                    margin-bottom: 30px;
                    @media (max-width: $breakpoint-tablet){
                        width: 32%;
                    }
                    @media (max-width: $breakpoint-mobile-big){
                        width: 100%;
                        margin-bottom: 20px;
                    }
                    .album_img{
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        img{
                            height: 290px;
                            object-fit: contain;
                            max-width: 100%;
                            @media (max-width: $breakpoint-tablet){
                                height: 200px;
                            }
                            @media (max-width: $breakpoint-mobile-big){
                                height: auto;
                            }
                        }
                    }
                    .album_info{
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin: 20px 0;
                        h4{
                            font-size: 1.6rem;
                            font-weight: lighter;
                        }
                        a{
                            display: inline-flex;
                            align-items: center;
                            justify-content: center;
                            background-color: $color-grey;
                            width: 35px;
                            height: 35px;
                            border-radius: 50%;
                            padding: 12px;
                            margin-left: 10px;
                            transition: 0.3s;
                            svg{
                                width: 100%;
                            }
                            &:hover{
                                background-color: $primary-color-yellow;
                            }
                        }
                    }

                    a{
                        font-size: 1.4rem;
                        display: inline-block;
                        text-decoration: underline;
                        transition: 0.3s;
                        &:hover{
                            color: $primary-color-yellow;
                        }
                    }
                }
            }

            .book_sec{
                margin-top: 30px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                background-color: $color-white;
                padding: 25px;
                border-radius: 5px;
                position: relative;
                @media (max-width: $breakpoint-mobile-big){
                    display: block;
                    padding: 15px;
                }
                .left_sec{
                    width: 35%;
                    @media (max-width: $breakpoint-tablet){
                        width: 45%;
                    }
                    @media (max-width: $breakpoint-mobile-big){
                        width: 100%;
                        padding-top: 20px;
                    }
                    img{
                        width: 100%;
                    }
                }
                .right_sec{
                    width: 60%;
                    @media (max-width: $breakpoint-tablet){
                        width: 50%;
                    }
                    @media (max-width: $breakpoint-mobile-big){
                        width: 100%;
                        margin-top: 15px;
                    }
                    .tooltip{
                        position: absolute;
                        right: 25px;
                        top: 0;
                        @media (max-width: $breakpoint-mobile-big){
                            right: 15px;
                        }
                    }
                    h5{
                        font-size: 2rem;
                        font-weight: lighter;
                    }
                    p, a{
                        margin-top: 20px;
                        @media (max-width: $breakpoint-tablet){
                            margin-top: 10px;
                        }
                    }
                    p{
                        br{
                            @media (max-width: $breakpoint-tablet){
                                display: none;
                            }
                        }
                    }
                }
            }

            .collection_popup{
                width: 70%;
                background-color: $color-grey;
                padding: 50px;
                @media (max-width: $breakpoint-tablet){
                    width: 90%;
                }
                @media (max-width: $breakpoint-mobile-big){
                    height: 80vh;
                    overflow-y: auto;
                    padding: 20px;
                }
                .closeBtn{
                    @media (max-width: $breakpoint-mobile-big){
                        right: 5px;
                        top: 5px;
                    }
                }
                .content{
                    display: flex;
                    justify-content: space-between;
                    @media (max-width: $breakpoint-mobile-big){
                        display: block;
                    }
                    .left_sec{
                        width: 45%;
                        @media (max-width: $breakpoint-tablet){
                            width: 40%;
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            width: 100%;
                        }
                        img{
                            width: 100%;
                            min-width: auto;
                        }
                    }

                    .right_sec{
                        width: 50%;
                        @media (max-width: $breakpoint-tablet){
                            width: 58%;
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            width: 100%;
                            margin-top: 15px;
                        }
                        h4{
                            font-size: 2rem;
                            font-weight: lighter;
                            span{
                                display: block;
                                font-size: 1.2rem;
                                margin-top: 5px;
                                @media (max-width: $breakpoint-mobile-big){
                                    font-size: 1.6rem;
                                }
                            }
                        }

                        .dimension{
                            margin-top: 30px;
                            @media (max-width: $breakpoint-tablet){
                                margin-top: 15px;
                            }
                            h5{
                                font-size: 1.4rem;
                                span{
                                    display: block;
                                    font-size: 1.4rem;
                                    font-weight: lighter;
                                    margin-top: 5px;
                                    @media (max-width: $breakpoint-mobile-big){
                                        font-size: 1.6rem;
                                    }
                                }
                            }
                        }

                        p{
                            margin-top: 30px;
                        }
                    }
                }
            }
        }        
    }
}