@media only screen and (max-width: 991px){
    .container{
        width: 100%;
        padding: 0 25px;
    }
    .pos_relative{
        position: relative;
    }

    .mt_70{
        margin-top: 50px;
    }

    header{
        background-color: $color-light-grey;   
        padding-bottom: 0;     
        .sub_header{
            display: none;
            background-color: $color-white;
            .container{
                width: 100%;
                padding: 0;
                ul{
                    width: 100%;                    
                    li{
                        a{
                            display: flex;
                            align-items: center;
                            border-bottom: none !important;
                            &.subnav_link{
                                &::after{
                                    &::after{
                                        content: "+";                                        
                                        background-size: 100%;                                                                                
                                        position: absolute;
                                        right: 15px;
                                        font-size: 2.5rem;
                                        font-weight: bold;
                                    }
                                }
                            } 
                        }
                        .sub_nav{
                            display: none;
                            background-color: $color-light-grey;
                            ul{
                                position: static;
                                box-shadow: none;                                
                                li{
                                    padding: 0;                                    
                                    a{
                                        text-align: left;
                                        border-bottom: none;
                                    }
                                    &:last-child{
                                        border-bottom: none;
                                    }
                                }                                
                            }
                        }                        
                    }
                }
            }            
        }
        .main_header{
            padding: 15px 0;
            .container{
                .nav{                    
                    position: absolute;
                    top: 0;
                    left: -100%;
                    right: 0;
                    background-color: #fff;
                    width: 100%;
                    height: 100%;
                    z-index: 1000;                    
                    padding-bottom: 70px;
                    overflow-y: auto;
                    transition: 1s;
                    &.show{
                        left: 0;                    
                    }
                    ul{
                        display: block;
                        li{                                                                                    
                            a{ 
                                padding: 15px 30px;
                                position: relative;
                                border-bottom: 1px $primary-color solid;
                                display: flex;
                                align-items: center;
                                &.subnav_link{
                                    &::after{
                                        content: "+";
                                        background-size: 100%;
                                        position: absolute;
                                        right: 15px;
                                        font-size: 2.5rem;
                                        font-weight: bold;
                                    }
                                }                                
                                span{
                                    display: none;
                                }
                            }
                            &.active{
                                a{
                                    &.subnav_link{
                                        &::after{
                                            content: "-";
                                        }
                                    }
                                }
                            }
                            .sub_nav_container{
                                display: none;                                
                                margin: 0;
                                padding: 0 30px; 
                                width: 100%;
                                position: static;
                                transition: none;
                                background-color: $color-light-grey;
                                ul{
                                    margin-left: 0;
                                    li{
                                        border-bottom: none;
                                        a{
                                            text-align: left;
                                            border-bottom: none;
                                            &:after{
                                                display: none;
                                            }
                                        }                                        
                                    }
                                    &::before{
                                        display: none;
                                    }
                                }
                                img{
                                    display: none;
                                }
                            }
                        }
                    }
                }
                .logo{
                    width: 35%;
                }

                .login_sec{
                    width: 50%;
                    .login_btn{
                        display: none;
                    }
                    .login_icon_btn{
                        display: block;
                        width: 35px;  
                        height: 35px;                      
                        background-color: $primary-color-yellow;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 5px;
                        align-self: stretch;
                        svg{
                            width: 100%;
                        }
                    }
                    .search_btn{
                        padding: 0;
                        width: 40px;
                        border-radius: 5px;
                    }
                    .search_box{
                        border: 1px $color-dark-grey solid;
                        padding: 10px;          
                        input[type=text]{
                            font-size: 1.4rem;
                        }          
                    }
                }

                .hamburger{
                    display: block;
                    width: 32px;
                    height: 25px;                    
                    margin-left: 15px;
                    span{
                        border-radius: 5px;
                        height: 4px;
                    }
                }                
            }
        }
    }

    .global_pl{
        padding-left: 30px;
    }

    .banner_sec{
        margin-top: 0;
    }

    .global_heading{
        padding: 0;
        h2{
            font-size: 2.5rem;
            &::before, &::after{
                bottom: 4px;
            }            
        }
        .auction_nav{
            display: none;
            margin-right: 0;
        }
    }

    .auction_container{
        .auction_way{
            display: block;
            margin-top: 15px;
            .auction_content, .auction_content p, .auction_slider_container{
                width: 100%;
            }
            .auction_slider_container{
                margin-top: 30px;
                padding-bottom: 50px;
            }
        }
    }

    .highlight_container{        
        margin-top: 70px;
    }

    .recent_auction_container{        
        margin: auto;
        margin-top: 50px;
        .recent_slider_container{            
            margin-top: 30px;            
        }
    }

    .price_container{ 
        margin-top: 50px;
        .price_slider_container{
            .swiper-wrapper{
                .item{
                    h4{
                        font-size: 2.8rem;
                    }
                }
            }
        }
    }

    .buy_sell_container{
        display: block;
        margin-top: 50px;
        &::after{
            display: none;
        }
        .buy_content{
            width: 100%;
            img{
                min-height: 150px;
                object-fit: cover;
            }
            .bug_sell_content{
                h4{
                    font-size: 2.5rem;
                }
            }
        }
    }

    .ourCollector_container {
        margin-top: 50px;
        .ourCollector_slider_container{
            padding-left: 30px;
            .swiper-wrapper{
                .item{
                    border-color: $primary-color;
                    .collectorProfile{
                        opacity: 1;
                    }
                }                
            }
        }
    }    

    .dashboard_container{
        .dashboard_links{
            width: 30%;
            .profile_links{
                ul{
                    li{
                        a{
                            font-size: 1.4rem;
                            padding-left: 20px;
                        }
                    }
                }
            }
        }
        .dashboard_sec{
            padding: 30px 15px;
            ul{
                width: 100%;
                &.col-3{
                    li{
                        width: 48%;
                        &.user_gender{
                            label{
                                margin-right: 20px;
                            }
                        }
                    }
                }
            }
            .bid_limit_box{
                width: 100%
            }
        }
    }

    .culture_container{
        .culture_content{        
            display: block;    
            .left_sec, .right_sec{
                width: 100%;
            }   
            .right_sec, .culture_text{
                margin-top: 30px;
            }            
        }
    }

    .values_container{
        .values_cont{
            display: block;
            .left_sec, .right_sec{
                width: 100%;
                .value_content{
                    margin-top: 20px;
                    .global-pagination{
                        position: static;
                        margin-top: 15px;
                    }
                }
            }   
            .right_sec{
                margin-top: 30px;
            }
        }
    }
}

@media screen and (min-width: 641px) and (max-width: 1199px){
    html{
        font-size: 58%;
    }

    body{  
        header{
            .main_header{ 
                .container{
                    .logo{
                        width: 22%;
                    }
                }
            }
        }   

        .banner_sec{
            .container{
                .left_sec{
                    .banner_content{
                        width: 95%;
                        h3{
                            font-size: 3.5rem;
                            line-height: 35px;
                        }
                        span, p{
                            font-size: 1.4rem;
                        }
                        p{
                            line-height: 25px;
                        }
                    }
                    .banner_nav{
                        .auction_nav{
                            margin-top: 10px;
                            width: 35%;
                        }
                    }
                }
            }
        }

        .global_heading{
            h2{
                &::before{
                    left: -70px;
                }
            }
            .auction_nav{
                display: flex !important;
                margin-right: 0;
                width: 15% !important;
            }
        }

        .highlight_slider_container{ 
            .highlight_slider{
                .swiper-wrapper{
                    .item{
                        padding: 20px;
                    }
                }
            }
        }

        .artist_category_container{
            &::after{
                left: 47.5;
            }
            .artist_content{
                .artist_info{
                    display: block;
                    .artist_image{
                        width: 70%;
                    }
                    .artist_info_content{
                        width: 90%;
                    }
                }
            }
        }

        .subscribe_container{
            .subscribe_content{
                h4{
                    font-size: 2.5rem;
                }
                p, form{
                    width: 90%;
                }            
            }
        }
        
        .login_reg_sec{
            .reg_form_sec{
                width: 700px;
                padding: 30px;
                form{
                    ul{
                        li{
                            margin: 20px;
                            width: 26%;
                        }
                    }
                }
            }
        }                  
        
        .management_container{
            .management_team{
                ul{
                    li{
                        width: 48%; 
                        margin-bottom: 50px;                       
                    }
                }
            }
        }

        .workenv_container{
            padding-bottom: 50px;
            &::after{
                content: none;                
            }
        }

        .heading_inner{
            .global_heading{
                width: 15%;
                .auction_nav{
                    width: 100% !important;
                }
            }
        }

        .employee_container{
            .employee_cont{
                .employee_slider{
                    .item{
                        .employeehead_cont{
                            .employee_name{
                                h4{
                                    font-size: 1.8rem;
                                    line-height: 22px;                                    
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 992px) and (max-width: 1199px){
    .auction_container {
        .auction_way{
            .auction_content{
                padding-bottom: 30px;
                p{
                    width: 85%;
                }
            }
        }
    }    
}

@media only screen and (max-width: 640px){
    html{
        font-size: 55%;
    }

    .banner_sec{
        .container{
            flex-flow: column;
            padding: 0;
            position: relative;
            .left_sec{
                width: 100%;
                order: 2;
                position: absolute;
                bottom: 0;
                z-index: 10;
                background-color: $primary-color;
                padding: 15px 25px;
                .banner_content{
                    width: 100%;
                    h3{
                        font-size: 3rem;
                        line-height: 35px;
                    }
                    span, p{
                        font-size: 1.4rem;
                    }
                    p{
                        line-height: 25px;
                    }
                    a{
                        margin-top: 15px;
                    }
                }
                .banner_nav{
                    position: absolute;
                    bottom: -30px;
                    left: 0;
                    right: 0;                    
                    .global-pagination{
                        margin: auto;
                        text-align: center;
                    }
                    .auction_nav{
                        display: none;                        
                    }
                }
            }
            .right_sec{
                width: 100%;
                .banner_slider{
                    .swiper-wrapper{
                        .item{
                            img{
                                height: auto;
                            }
                            &.swiper-slide-active{
                                transform: none;
                                img{
                                    min-height: auto;                                                                    
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .auction_container{
        margin-top: 70px;
        .auction_way{
            .auction_content{                
                a{
                    margin-top: 15px;
                }
                .global-pagination{
                    bottom: 0px;
                }
            }
            .auction_slider_container{
                .auction_slider{                                    
                    .swiper-wrapper{
                        .item{
                            padding-bottom: 5px;
                            .auction_info{
                                .auction_date{
                                    h4{
                                        font-size: 1.6rem;
                                    }
                                    span{
                                        margin-top: 5px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .highlight_container{
        margin-top: 50px;
        .highlight_slider_container{
            .highlight_slider{
                overflow: visible;
                .swiper-wrapper{                    
                    .item{
                        padding: 15px;
                        border-radius: 5px;
                        border-right: none;
                        box-shadow: 0px 0px 20px 15px rgba(238,238,238,1);                        
                        img{
                            height: 150px;
                        }
                        .highlight_detail{
                            .highlight_name{                                
                                span{                                    
                                    margin-top: 5px;
                                }
                            }
                        }                        
                    }
                }
            }            
        }
    }

    .services_container{
        margin-top: 50px;
        .container{
            overflow: visible;
            .services_slider{
                padding-bottom: 0;
                .swiper-wrapper{
                    .item{
                        .services_content{
                            h5{
                                margin-top: 0;
                                width: 100%;
                            }
                        }
                        img{
                            margin-left: 20px;
                        }                        
                        &:hover, &.swiper-slide-next{
                            transform: scale(1);
                        }
                    }
                }
            }
        }
        .services_btn{
            position: relative;
            margin-top: 15px;
        }
    }

    .price_container{
        .price_tabs{
            ul{                
                justify-content: flex-start;                
                li{
                    flex-grow: 1;
                    flex-shrink: 0;
                    margin-right: 20px;
                    margin-left: 0;
                    a{
                        font-size: 1.4rem;
                        text-align: center;                        
                    }
                }
            }
        }

        .price_slider_container{
            padding-bottom: 50px;
            .container{
                .modern_slider{     
                    padding-bottom: 0;           
                    .swiper-wrapper{                        
                        .item{
                            padding: 15px;
                            padding-bottom: 30px;
                            margin-bottom: 30px;                            
                            h4{
                                font-size: 2.5rem;
                            }
                        }
                    }
                }
                .price_btn{
                    position: static;
                    margin-top: 30px;
                }
            }
            .auction_nav{
                display: none;
            }
        }
    }

    .month_container{
        margin-top: 50px;
    }

    .artist_category_container{
        display: block;
        &::after{
            width: 100%;
            height: 1px;
            left: 0;
            right: 0;
            margin: auto;
            top: 46%;
        }
        .artist_content{
            width: 100%;
            margin: auto;
            .artist_info{
                display: block;
                .artist_image, .artist_info_content{
                    width: 100%;
                }
                .artist_info_content{
                    p{
                        line-height: 25px;
                    }
                    a{
                        margin-top: 20px;
                    }
                }
            }
        }

        .category_content{
            margin-top: 70px;
            .global_heading{
                h2{
                    &::before{
                        left: -100px;
                        right: auto;
                    }
                }
            }
        }
    }

    .ourCollector_container{
        .container{        
            .rightSliderNav{
                display: none;
            }
            .ourCollector_slider_container{
                padding-left: 0;
            }

            .global-pagination{
                display: block;
                position: static;
                margin-top: 20px;
                text-align: center;
            }
        }
    }

    .news_container{
        margin-bottom: 50px;
        .container{        
            .news_tab_content{            
                .news_content{
                    display: block;
                    .left_sec{
                        width: 100%;
                        .big_news{
                            display: block;
                            .big_news_img, .big_news_content{
                                width: 100%;
                                p, a{
                                    margin-top: 15px;
                                }                                
                            }
                        }
                    }

                    .right_sec{
                        width: 100%;
                        margin-top: 20px;
                        ul{
                            li{
                                a{
                                    img{
                                        max-width: 90px;
                                    }
                                    h5{
                                        font-size: 1.6rem;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .subscribe_container {
        padding: 50px 15px;
        .subscribe_content{
            width: 90%;
            margin: auto;
            h4{
                font-size: 2.5rem;
            }
            p{
                width: 100%;
            }
            form{                
                width: 100%;
                ul{
                    display: block;
                    li{
                        width: 100%;
                        margin: 0;
                        margin-bottom: 30px;    
                        &:last-child{
                            text-align: center;
                        }                    
                    }
                }
            }
        }
    }

    footer{        
        padding-top: 50px;
        .main_footer{
            display: block;
            width: 90%;
            padding: 0 15px;            
            .footer_list{
                width: 100%;
                margin-bottom: 30px;
                border-bottom: 1px $primary-color solid;
                &:last-child{
                    border-bottom: none;
                    h5{
                        &::after{
                            content: none;
                        }
                    }
                }
                h5{
                    padding-bottom: 15px;
                    font-size: 1.6rem;
                    position: relative;
                    &::after{
                        content: "prev";
                        font-family: swiper-icons;
                        font-size: 1.5rem;
                        transform: rotate(270deg);
                        position: absolute;
                        right: 5px;
                        color: $primary-color;
                        font-weight: bold;
                    }
                }
                ul{
                    display: none;
                    margin-top: 10px;
                    padding-bottom: 30px;
                }

                .app_links{
                    margin-top: 10px;
                    display: flex;
                    align-items: center;
                    a{
                        margin-bottom: 0;
                        img{
                            width: 80%;
                        }
                    }
                }

                .social_links{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    h5{
                        padding-bottom: 0;
                        margin-right: 50px;
                    }
                    a{
                        margin-top: 0;
                        &:last-child{
                            margin-right: 0;
                        }
                    }
                }
            }
        }

        .sub_footer{
            .container{
                display: block;
                width: 100%;
                .left_sec{
                    ul{
                        justify-content: center;
                        li{
                            margin-right: 10px;
                            a{
                                text-align: center;
                            }
                        }
                    }
                }
                .right_sec{
                    margin-top: 20px;
                    p{
                        text-align: center;
                    }
                }
            }
        }
    }

    body{    
        .login_reg_sec{
            width: 100%;
            .closeBtn{
                right: -10px;
            }
            h3{
                font-size: 2.5rem;
            }
            form{
                margin-top: 20px;
            }
            .login_form_sec, .reg_form_sec, .forgot_form_sec{
                width: 90%;
                margin: auto;
                padding: 25px ;
            }
            .reg_form_sec{
                ul{
                    display: block;
                    li{
                        width: 100%;
                        margin: 0;
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }

    .dashboard_container{
        display: block;

        .dashboard_links{
            width: 100%;
            padding: 30px 0;
        }

        .dashboard_sec{
            width: 100%;
            padding: 30px 20px;
            ul{
                width: 100%;
                &.col-6, &.col-3{
                    li{
                        width: 100%;
                    }
                }
            }
            .user_info{
                ul{
                    display: block;
                    li{
                        width: 100%;
                        margin-bottom: 0px;
                        padding: 20px 0;
                        &.user_interested{
                            span{
                                position: static;
                                margin-top: 5px;
                            }
                        }                        
                    }                    
                }
            }
            .bid_limit_box{
                width: 100%;
                ul{
                    li{
                        margin-bottom: 20px;
                        &:last-child{
                            margin-bottom: 0;
                        }
                    }
                }
            }
        }
    }

    .who_banner{
        img{
            min-height: 350px;
            object-fit: cover;
            margin-bottom: 0;
        }

        .heading_inner{
            margin-top: 30px;
        }
    }

    .culture_container{
        .culture_content{
            .left_sec{
                .global_heading{
                    .auction_nav{
                        width: 30%;
                        display: flex;
                    }
                }
            }
        }
    }

    .management_container{
        .management_team{
            margin-top: 20px;
            ul{
                li{
                    width: 100%;                    
                    margin-bottom: 50px;
                }
            }
        }
    }

    .workenv_container{
        .container_inner{
            display: block;
            .left_sec, .right_sec{
                width: 100%;
            }
            .right_sec{
                margin-top: 30px;
            }
        }
    }

    .employee_container{
        .employee_cont{
            .employee_slider{
                .item{
                    padding: 15px;
                    .employeehead_cont{
                        display: block;
                        .employee_name{
                            margin-left: 0;
                            text-align: center;
                            h4{
                                margin-top: 10px;
                                font-size: 1.8rem;
                                line-height: 22px;                                    
                            }
                        }
                    }
                }
            }
        }
    }

}

@media screen and (min-width: 1600px){
    html{
        font-size: 70%;
    }
}

@media only screen and (max-width: 1365px){
    html{
        font-size: 50%;
    }
}