.price_container{
    .price_tabs{
        margin-top: 30px;
        ul{
            display: inline-flex;
            width: 100%;
            overflow-x: auto;
            align-items: center;
            justify-content: center;
            flex-wrap: inherit;
            -ms-overflow-style: none;
            overflow: -moz-scrollbars-none;
            &::-webkit-scrollbar {
                display: none;
            }
            li{
                margin: 0 20px;
                a{
                    display: block;
                    font-size: 1.8rem;                    
                    transition: 0.3s;
                    position: relative;
                    padding-bottom: 10px;                    
                    border-bottom: 2px $color-white solid;                    
                    &:hover, &.active{
                        font-weight: bold;
                        border-color: $primary-color-yellow;
                    }                    
                }
            }
        }
    }

    .price_slider_container{        
        background-color: $color-light-grey;        
        padding-bottom: 70px;      
        position: relative;    
        .container{            
            overflow: hidden;
            .modern_slider{
                padding: 50px 0;
                    .swiper-wrapper{                        
                        .item{
                            background-color: $color-white;
                            text-align: center;
                            padding: 30px;
                            border-radius: 10px;
                            position: relative;
                            padding-bottom: 50px;
                            &.swiper-slide-next{
                                box-shadow: 0px 0px 20px 15px rgba(232,227,232,1);
                            }                            
                            img{
                                max-width: 100%;
                                max-height: 250px;
                                object-fit: cover;
                            }
                            h5{
                                font-size: 2rem;
                                font-weight: lighter;
                                margin-top: 20px;
                            }
                            p{                    
                                font-size: 1.4rem;
                                margin-top: 10px;
                            }
                            h4{
                                font-size: 2.2rem;
                                font-weight: bold;
                                margin-top: 25px;
                            }
                            .record_lot{
                                position: absolute;
                                bottom: -12px;
                                margin: auto;
                                text-align: center;
                                left: 0;
                                right: 0;
                                span{
                                    background-color: $color-grey;
                                    font-size: 1.2rem;
                                    display: inline-block;
                                    padding: 5px 20px;
                                    border-radius: 15px;
                                    line-height: normal;
                                    text-transform: uppercase;
                                }
                            }
                        }
                    }
                }
        }

        .price_btn{
            position: absolute;
            bottom: -18px;
            left: 0;
            right: 0;
            margin: auto;
            text-align: center;            
        }

        .auction_nav{
            display: flex;
            justify-content: space-between;
            align-items: center;    
            width: 93%;
            margin: auto;            
            position: absolute;
            top: 45%;
            left: 0;
            right: 0;                        
            .slider_nav{            
              position: static;
              margin-top: 0;
              background-color: $primary-color-yellow;
              width: 30px;
              height: 30px;
              border-radius: 50%;
              padding: 18px;      
              &::after{
                font-size: 12px;
                font-weight: bold;
                color: $primary-color;
              }        
            }
          }
    }
}