.dept_container{
    margin: 50px auto;    
    .dept_content{
        ul{
            -webkit-column-count: 4;
            -moz-column-count:4;
            column-count: 4;
            -webkit-column-gap: 15px;
            -moz-column-gap: 15px;
            column-gap: 15px;            
            padding: 0;            
            @media (max-width: $breakpoint-tablet){
                -webkit-column-gap: 10px;
                -moz-column-gap: 10px;
                column-gap: 10px;                
            }
            @media (max-width: $breakpoint-mobile-big){
                -webkit-column-count: 2;
                -moz-column-count:2;
                column-count: 2;
            }
            li{
                margin-bottom: 15px;
                position: relative;
                transition: .3s ease;
                overflow: hidden;
                @media (max-width: $breakpoint-tablet){
                    margin-bottom: 10px;
                }
                
                img{
                    width: 100%;
                    transition: transform .5s ease;
                }
                .dept_head{
                    visibility: hidden;                    
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0,0,0,0.7);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    h4{
                        font-size: 2rem;
                        color: $primary-color-yellow;
                        font-weight: lighter;
                        text-align: center;
                        @media (max-width: $breakpoint-mobile-big){
                            font-size: 1.6rem;
                            line-height: 20px;
                            padding: 0 15px;
                        }
                    }
                }
                &:hover{
                    img{
                        transform: scale(1.3);
                    }
                    .dept_head{
                        visibility: visible;
                    }
                }
            }        
        }
    }
}