.banner_sec{    
    background-color: $primary-color;
    .container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left_sec{
            width: 33%;
            .banner_content{   
                width: 80%;
                position: relative;
                span{
                    display: block;
                    color: $color-white;
                    font-size: 1.6rem;                    
                    text-transform: uppercase;
                }
                h3{
                    font-size: 5rem;
                    line-height: 60px;
                    font-weight: lighter;
                    margin-top: 15px;
                    color: $primary-color-yellow;
                }
                p{
                    color: $color-white;
                    font-size: 1.6rem;                    
                    margin-top: 15px;
                    line-height: 30px;
                }
                a{
                    margin-top: 30px;
                }                
            }

            .banner_nav{
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-top: 50px;
                .global-pagination{  
                    position: static; 
                    width: auto;                 
                }
                .auction_nav{
                    width: 20%;
                    margin-top: 10px;
                    .slider_nav{
                        width: 30px;
                        height: 30px;                        
                        &:after{
                            font-size: 2.5rem;
                            color: $primary-color-yellow;
                        }                        
                    }
                }
            }
        }
        

        .right_sec{
            width: 65%;
            font-size: 0;
            .banner_slider{
                overflow: hidden;
                .swiper-wrapper{
                    align-items: center;
                    .item{                        
                        &.swiper-slide-active{
                            transform: scale(1.5);
                            height: auto;
                            img{
                                min-height: 85vh;
                                object-fit: cover;                                
                            }
                        }
                        img{
                            width: 100%;
                            height: 320px;
                            object-fit: cover;
                        }                        
                    }
                }                                
            }            
        }
    }
}