.login_reg_sec{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2000;    
    display: none;
    
    .login_form_sec, .password_form_sec, .forgot_form_sec{
        width: 400px;        
        padding: 50px;
        position: relative;        
        background: $color-white;        
    }

    .login_form_sec{
        .login_des{
            margin-top: 30px;
            padding-bottom: 15px;            
            .custom_radio_cont{
                justify-content: center;
            }
        }

        .form_sec{            
            margin-top: 30px;        
            .password_sec{
                display: flex;
                align-items: center;
                position: relative;
                a{
                    width: 20px;
                    img{
                        width: 100%;
                    }
                    &.forgot_pass{
                        width: auto;
                        display: inline-block;
                        position: absolute;
                        bottom: -25px;
                        right: 0;
                        font-size: 1.2rem;
                        font-style: italic;
                        transition: 0.3s;
                        &:hover{
                            color: $primary-color-yellow;
                        }
                    }
                }
            }
            input[type=submit]{
                margin-top: 15px;
            }
            &.mobile_form, .verify_btn, .enter_otp{
                display: none;
            }
        }
    }    

    .forgot_form_sec{
        display: none;        
    }

    .reg_form_sec{
        display: none;
        position: relative;
        width: 900px;
        padding: 50px;    
        background: $color-white;
        height: 90vh;
        overflow-x: visible;
        overflow-y: auto;
        scrollbar-width: thin;
        scrollbar-color: $primary-color $color-grey;
        &::-webkit-scrollbar {
            width: 6px;
        }   
        &::-webkit-scrollbar-track {
            background-color: $color-grey;                    
            border-radius: 10px;
        }                                
        &::-webkit-scrollbar-thumb {
            background: $primary-color;
            border-radius: 10px;
        }  
        .closeBtn{
            right: 15px;
            top: 15px;
        }
        .profile_tabs{
            .container{
                .left_sec{
                    width: 100%;
                    @media (max-width: $breakpoint-mobile-big){
                        margin-top: 0;
                    }
                    ul{
                        width: 45%;
                        margin: auto;
                        justify-content: center;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 100%;
                            overflow-x: auto;
                            justify-content: flex-start;
                            align-items: center;
                            flex-wrap: inherit;
                            scrollbar-width: thin;
                            scrollbar-color: $primary-color $color-grey;
                            &::-webkit-scrollbar {
                                width: 6px;
                                height: 5px;
                            }   
                            &::-webkit-scrollbar-track {
                                background-color: $color-grey;                    
                                border-radius: 10px;
                            }                                
                            &::-webkit-scrollbar-thumb {
                                background: $primary-color;
                                border-radius: 10px;
                            } 
                        }
                        li{
                            flex-grow: 1;
                            flex-shrink: 0;
                            @media (max-width: $breakpoint-mobile-big){
                                width: auto;
                                margin-right: 20px;
                                &:last-child{
                                    margin-right: 0;
                                }
                                &.arrow_icon{
                                    display: none;
                                }
                            }
                            a{
                                .svg_sec{
                                    @media (max-width: $breakpoint-mobile-big){
                                        display: none;
                                    }                                
                                }
                                span{
                                    br{
                                        @media (max-width: $breakpoint-mobile-big){
                                            display: none;
                                        }                                    
                                    }
                                }
                            }
                        }
                    }                
                }
            } 
        }
        
        .profile_tabbing{
            .container{
                width: 100%;
                margin: 0 auto;
                box-shadow: none;
                padding: 0;
                .profile_content{
                    .form_content{
                        ul{
                            li{
                                margin-bottom: 30px;
                                &.non_indian{
                                    display: none;
                                }
                                .phone_no{
                                    .code{
                                        width: 20%;
                                        padding: 0;
                                        text-align: center;
                                    }
                                }
                            }
                        }                    
                    }                
                }
                .save_btn{
                    text-align: center;
                    margin-bottom: 15px;
                }
            }
        }
    }    

    .closeBtn{
        position: absolute;
        right:-15px;
        top:-15px;
        background-color: $primary-color-yellow;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer;
        transition: 0.3s;
        box-shadow: -3px 3px 5px 0px rgba(0,0,0,0.5);               
        &:hover{
            background-color: $primary-color;
            color: $color-white;
        }
    }

    h3{
        font-size: 3rem;
        color: $primary-color;
        text-align: center;
        font-weight: lighter;
        span{
            display: block;
            font-size: 1.2rem;
            font-style: italic;
            margin-top: 10px;            
        }
    }

    form{
        margin-top: 30px;
        ul{
            li{
                margin-bottom: 30px;                
                position: relative;            
                input[type=text], input[type=password]{
                    font-size: 1.6rem;
                    font-family: $font-italic;
                    padding: 10px 15px;                    
                    width: 100%;
                    &:focus{
                        &::placeholder{
                            color: transparent;
                        }
                    }
                }
                &:last-child{
                    text-align: center;
                    border-bottom: none;
                    margin-bottom: 0;
                    input[type=submit]{
                        padding: 15px 50px 10px;
                        font-weight: normal;
                    }
                }
                &.password_sec{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    a{
                        display: inline-block;
                        width: 30px;
                        img{
                            width: 100%;
                        }
                    }
                }                
            }
        }
    }

    .create_acc{
        text-align: center;
        margin-top: 20px;
        a{
            display: inline-block;
            font-size: 1.6rem;
            text-decoration: underline;
            transition: 0.3s;
            &:hover{
                color: $primary-color-yellow;
            }
        }
    }
}