footer{
    padding-top: 70px;
    background-color: $color-light-grey;
    .container{
        width: 85%;
        margin: auto;
    }
    .main_footer{
        display: flex;
        justify-content: space-between;
        .footer_list{
            width: 25%;
            h5{
                font-size: 2rem;
                font-weight: lighter;
                color: $primary-color;
            }
            ul{
                margin-top: 30px;
                li{
                    margin-top: 20px;
                    &:first-child{
                        margin-top: 0;
                    }
                    a{
                        display: block;
                        font-size: 1.6rem;
                        color: $footer-grey;
                        transition: 0.3s;
                        &:hover{
                            color: $primary-color;
                        }
                    }
                }
            }
            .app_links{
                margin-top: 30px;
                a{
                    display: block; 
                    margin-bottom: 30px;
                    img{
                        width: 50%;
                    }                   
                }
            }

            .social_links{
                margin-top: 50px;
                a{                                      
                    margin-top: 15px;
                    margin-right: 10px;
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    padding: 10px;
                    display: inline-flex;
                    align-items: center;
                    background-color: $primary-color-yellow;
                    transition: 0.3s;
                    svg{
                        width: 100%;
                    }
                    &:hover{
                        background-color: $primary-color;
                        svg{
                            path{
                                fill: $primary-color-yellow;
                            }
                        }
                    }
                }
            }
        }
    }
}

.sub_footer{
    background-color: $color-white;
    margin-top: 50px;
    padding: 15px 0;
    .container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            display: block;
            width: 100%;
        }            
        .left_sec{
            ul{
                display: flex;
                align-items: center;
                @media (max-width: $breakpoint-mobile-big){
                    justify-content: center;
                }
                li{
                    margin-right: 20px;
                    @media (max-width: $breakpoint-mobile-big){
                        margin: 0 8px;
                    }
                    a{
                        display: block;
                        font-size: 1.2rem;
                        color: $primary-color;
                        transition: 0.3s;
                        padding: 10px 0;
                        @media (max-width: $breakpoint-mobile-big){
                            text-align: center;
                        }
                        &:hover{
                            color: $primary-color-yellow;
                        }
                    }
                }
            }
        }
        .right_sec{
            @media (max-width: $breakpoint-mobile-big){
                margin-top: 20px;
            }
            p{
                font-size: 1.2rem;
                @media (max-width: $breakpoint-mobile-big){
                    text-align: center;
                }
            }
        }
    }
}