.filter_head{
    background-color: $primary-color;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    @media (max-width: $breakpoint-tablet){
        padding: 10px 20px;
    }
    @media (max-width: $breakpoint-mobile-big){
        background-color: $color-light-grey;
        border-bottom: 1px #b7b7b7 solid;
    }
    div{
        display: flex;
        align-items: center;
        img{
            width: 20px;
            @media (max-width: $breakpoint-mobile-big){
                display: none;
            }
        }
        span{
            display: inline-block;
            color: $color-white;
            font-size: 2rem;
            margin-left: 10px;
            @media (max-width: $breakpoint-mobile-big){
                color: $primary-color;
                margin-left: 0;
            }
        }                    
    }
    a{
        &.clear_btn{
            display: inline-block;
            color: $color-white;
            font-size: 1.4rem;
            text-decoration: underline;
            @media (max-width: $breakpoint-mobile-big){
                display: none;
            }
        }

        &.close{
            display: none;
            @media (max-width: $breakpoint-mobile-big){
                display: block;
                width: 15px;
                img{
                    width: 100%;
                }
            }
        }
    }
}

.check_cont{
    margin: 25px 20px;
    margin-bottom: 0;
    padding-bottom: 25px;
    border-bottom: 1px #b7b7b7 solid;
    span{
        display: block;
        font-size: 1.4rem;
        text-transform: uppercase;
        font-weight: bold;
    }    

    .custom-checkboxWrap{
        margin-top: 15px;
        li{
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
            }
            .custom_checkbox{
                .checkmark{
                    background-color: $color-white;
                    border: 1px $primary-color solid;
                }
                &:hover input ~ .checkmark {
                    background-color: $color-grey;
                }
                input:checked ~ .checkmark {
                    background-color: $primary-color;
                    &::after{
                        left: 6px;
                        top: 2px;
                    }
                }
            }
        }
        &.col-2{
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            li{
                width: 48%;
                &:last-child{
                    margin-bottom: 15px;
                }
            }
        }
    }

    &.bid_range{
        .range_slider{
            margin: 20px 0;
            border: none;
            background-color: $color-light-grey;
            @media (max-width: $breakpoint-mobile-big){
                background-color: $color-dark-grey;
            }
            .ui-slider-range{
                background-color: $primary-color;
            }
            .ui-slider-handle{
                border-radius: 50%;
                background-color: $primary-color;
                border: 2px $color-white solid;
            }
        }
        input[type=text]{
            font-size: 1.4rem;
            font-weight: bold;
            @media (max-width: $breakpoint-mobile-big){
                padding: 5px 0;
                background: transparent;
            }
        }
    }
}

.tag_sec{
    display: none;
    margin-top: 20px;
    padding: 0 20px;    
    align-items: center;
    justify-content: space-between;
    @media (max-width: $breakpoint-mobile-big){
        display: flex;
    }
    ul{
        li{
            a{
                background-color: $primary-color;
                display: inline-flex;
                justify-content: space-between;
                align-items: center;
                color: $color-white;
                min-width: 120px;
                padding: 10px;
                border-radius: 5px;
                cursor: initial;
                margin-bottom: 10px;
                span{
                    font-size: 1.4rem;
                }                     
                img{
                    width: 12px;
                    &:hover{
                        cursor: pointer;
                    }
                }           
            }
        }
    }
    .clear{
        font-size: 1.4rem;
        text-decoration: underline;
    }
}

.apply_filter{
    display: none;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    bottom: 10px;
    @media (max-width: $breakpoint-mobile-big){
        display: block;
        a{
            padding: 10px 70px;
        }
    }
}