.common_form{
    ul{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: flex-end;
        &.col-6{
            li{
                width: 48%;
                @media (max-width: $breakpoint-mobile-big){
                    width: 100%;
                }
                &.full{
                    width: 100%;
                }
            }
        }
        li{
            .custom_input{
                input{
                    color: $color-white;
                }
            }
        }
    }
    &.white_form{
        ul{
            li{
                .custom_input input[type=text], select{
                    color: $color-white;
                    border-color: $color-white;
                    &::placeholder{
                        color: $color-white;
                    }
                }
                select{
                    color: $color-white;
                    background: url("../../images/downarrow-white.svg") no-repeat right;
                    background-size: 10px;
                    background-position-y: 2px;
                    option{
                        color: $primary-color;                        
                    }
                }
            }
        }
    }
}