.artist_info_container{
    background-color: $color-light-grey;
    padding: 50px 0;
    margin-top: 30px;
    .container{
        display: flex;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            display: block;
        }
        .left_sec{
            width: 20%;
            @media (max-width: $breakpoint-tablet){
                width: 35%;
            }
            @media (max-width: $breakpoint-mobile-big){
                width: 70%;                
                margin: auto;
            }
            img{
                width: 100%;
            }
        }

        .right_sec{
            width: 75%;
            @media (max-width: $breakpoint-tablet){
                width: 60%;
            }
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                margin-top: 30px;
            }
            h4{
                font-size: 2rem;
                font-weight: lighter;
            }
            span{
                display: block;
                font-size: 1.6rem;
                font-weight: bold;
                margin-top: 10px;
            }
            p{
                margin-top: 30px;
                width: 80%;
                @media (max-width: $breakpoint-tablet){
                    width: 100%;
                }
            }
        }
    }
}

.stories_tabcontent{
    margin-top: 30px;
    .story_slider{
        overflow: hidden;
        .item{
            .story_image{
                font-size: 0;
                position: relative;
                img{
                    width: 100%;
                }
                .playBtn{
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    img{
                        width: 60px;
                    }
                }
            }            
            .story_content{
                background-color: $color-light-grey;
                padding: 15px 30px;
                h4{
                    font-size: 1.8rem;
                    font-weight: lighter;
                    line-height: 25px;
                }
                .story_date_share{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-top: 15px;
                    span{
                        font-size: 1.4rem;
                        display: inline-block;
                    }
                    svg{
                        width: 30px;
                    }
                }
            }
        }
        .global_heading{
            margin-top: 50px;
            justify-content: center;
            .auction_nav{
                @media (max-width: $breakpoint-mobile-big){
                    display: flex;
                    width: 30%;
                }                
            }
        }
    }    
}