.artist_category_container{
    position: relative;
    &::after{
        content: "";
        width: 1px;
        height: 100%;
        background-color: $color-grey;
        position: absolute;
        top: 0;
        left: 50%;
    }
    display: flex;
    justify-content: space-between;
    .artist_content{
        width: 48%;        
        .artist_info{
            display: flex;
            justify-content: space-between;
            margin-top: 30px;

            .artist_image{
                width: 40%;
                img{
                    width: 100%;
                }
            }

            .artist_info_content{
                width: 55%;                
                h5{
                    font-size: 2rem;                    
                    font-weight: lighter;
                    margin-top: 20px;
                }
                p{
                    margin-top: 15px;
                    font-size: 1.6rem;
                    font-family: $font-regular;
                    line-height: 30px;
                }
                a{
                    margin-top: 30px;
                }
            }
        }
    }

    .category_content{        
        .global_heading{
            h2{
                &::before{
                    left: auto;
                    right: -100px;
                }
            }
        }
    }
}