.latest_jobs_container{
    background-color: $color-light-grey;
    padding: 50px 0;

    .latestjobs_cont{
        margin-top: 30px;
        position: relative;
        .latestjobs_slider{
            overflow: hidden;
            .item{
                background-color: $color-white;
                border-radius: 5px;
                padding: 30px;
                .job_head_cont{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    .job_head{
                        h4{
                            font-size: 2.2rem;
                            font-weight: lighter;
                        }
                        span{
                            display: block;
                            font-size: 1.4rem;
                            font-weight: bold;
                            margin-top: 10px;
                        }
                    }
                    .job_share{
                        a{
                            background-color: $color-light-grey;
                            padding: 12px;
                            border-radius: 50%;
                            width: 40px;
                            height: 40px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            margin: auto;
                            margin-bottom: 10px;
                            transition: 0.3s;
                            svg{
                                width: 100%;
                            }
                        }
                    }
                }
                p{
                    margin-top: 20px;
                    font-size: 1.4rem;
                    line-height: 30px;
                }

                a{
                    margin-top: 20px;
                }
            }            
        }
        .global_heading{
            position: absolute;
            left: -20px;
            right: -20px;
            top: 45%;
            .auction_nav{
                width: 100% !important;
                margin-right: 0;
            }
        }
    }
}

.workenv_container{
    background-color: $primary-color;
    padding-top: 50px;
    position: relative;
    &::after{
        content: "";
        width: 100%;
        background-color: $color-white;
        height: 70px;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
    }
    .container{
        display: flex;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            display: block;
        }
        .left_sec{
            width: 57%;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }
            .heading_inner{
                display: block;
                h3{
                    color: $primary-color-yellow;
                }
                p{
                    color: $color-white;
                    font-size: 1.4rem;
                    line-height: 30px;
                    margin-top: 20px;
                    font-weight: lighter;
                }
            }            
        }

        .right_sec{
            width: 40%;
            position: relative;
            z-index: 50;
            font-size: 0;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                margin-top: 30px;
            }
            img{
                width: 100%;
                position: relative;
                z-index: 10;
            }
            a{
                display: inline-block;
                width: 60px;
                height: 60px;
                margin: 0 auto;
                position: absolute;
                left: 0;
                right: 0;
                top: 45%;                
            }
        }
    }

    &.applyjob_cont{
        padding-bottom: 50px;
        &::after{
            content: none;
        }
        .left_sec{
            width: 50%;
        }
        .right_sec{
            width: 45%;
            margin-top: 50px;
            .white_form{
                li{         
                    margin-bottom: 20px;       
                    .custom_input{
                        input[type=text]{
                            color: $color-white;
                            border-color: $color-white;
                            &::placeholder{
                                color: $color-white;   
                            }
                        }
                    }
                    .file_sec{
                        display: flex;
                        align-items: flex-end;
                        justify-content: space-between;     
                        .fileUpload{
                            &::before{
                                content: "Browse";
                            }
                        }                   
                    }
                }               
            }
        }
    }
}

.employee_container{
    .employee_cont{
        margin-top: 30px;
        overflow: hidden;
        .employee_slider{
            padding: 30px;
            .item{
                background-color: $color-white;
                box-shadow: 0px 0px 15px 12px #eee;
                padding: 30px;
                border-radius: 5px;
                .employeehead_cont{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    .employee_img{
                        max-width: 170px;
                        img{
                            width: 100%;
                        }
                    }
                    .employee_name{
                        margin-left: 30px;
                        h4{
                            color: $primary-color-yellow;
                            font-size: 2.5rem;
                            font-weight: lighter;
                            line-height: 30px;
                        }
                        span{
                            display: block;
                            font-size: 1.4rem;
                            margin-top: 10px;
                        }
                    }
                }
                p{
                    font-size: 1.4rem;
                    line-height: 30px;
                    margin-top: 30px;
                    text-align: center;
                    @media (max-width: $breakpoint-mobile-big){
                        margin-top: 15px;
                    }
                }

                .quote_icon{
                    text-align: right;
                    margin-top: 30px;                    
                    img{
                        width: 50px;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 30px;                                                    
                        }
                    }
                }
            }
        }        
    }
}

.subscribe_container{
    &.mt_70{
        margin-top: 0;
    }    
}