header{
  border-bottom: 2px $color-light-grey solid;
  padding-bottom: 20px;
  .sub_header{
    background-color: $color-light-grey;
    ul{
      display: flex;
      justify-content: flex-end;
      li{
        position: relative;
        a{
          font-size: 1.3rem;
          display: block;
          padding: 10px 20px;          
          &:hover{
            color: $primary-color-yellow;
            .sub_nav{
              display: block;
            }
          }
        }
        &:last-child{
          a{
            padding-right: 0;
          }
        }
        .sub_nav{      
          visibility: hidden;
          opacity: 0;
          transition: 0.2s;          
          ul{            
            display: block;
            background-color: $color-light-grey;
            position: absolute;
            left: -50%;
            right: 0;
            width: 100%;
            min-width: 200px;
            top: 100%;
            z-index: 300;
            box-shadow: 0px 20px 50px #ccc;
            li{
              padding: 0 30px;
              a{
                text-align: center;
                border-bottom: 1px $primary-color solid;                
              }
              &:last-child{
                a{
                  padding-right: 0;
                  border-bottom: none;
                }
              }
            }
          }          
        }
        &:hover{
          .sub_nav{
            visibility: visible;
            opacity: 1;
          }
        }
      }      
    }    
  }

  .main_header{
    padding-top: 20px;
    .container{          
      display: flex;
      align-items: center;
      justify-content: space-between;

      .logo{
        width: 12%;
        position: relative;
        z-index: 200;
        a{
          display: inline-block;
          img{
            width: 100%;
          }
        }
      }

      .nav{
        width: 45%;
        ul{
          display: flex;
          align-items: center;
          justify-content: space-between;
          li{
            position: relative;            
            a{
              display: block;
              font-size: 1.6rem;
              text-transform: uppercase;
              transition: 0.3s;
              &:hover{
                color: $primary-color-yellow;
              }        
            }
            .sub_nav_container{
              position: absolute;              
              left: 0;
              top: 100%;
              padding: 50px 1000em;
              padding-bottom: 20px;
              margin: 0 -1000em;
              z-index: 100;
              background-color: $color-white;
              display: flex;
              align-items: center;
              visibility: hidden;
              opacity: 0;      
              transition: 0.5s;        
              ul{
                display: block;
                min-width: 150px;
                position: relative;
                margin-left: -60px;
                &::before{
                  content: "";
                  width: 0; 
                  height: 0; 
                  border-left: 6px solid transparent;
                  border-right: 6px solid transparent;                  
                  border-top: 6px solid $primary-color;
                  position: absolute;
                  right: 0;
                  top: -25px;
                }
                li{
                  a{
                    border-bottom: 1px $primary-color solid;
                    text-align: right;
                    padding: 10px 0;                    
                  }
                  &:last-child{
                    a{
                      border-bottom: none;
                    }                  
                  }
                }                
              }

              img{
                margin-left: 50px;
                max-width: 260px;
              }
            }
            &:hover{
              .sub_nav_container{              
                visibility: visible;
                opacity: 1;                
              }
            }
          }
        }
      }

      .login_sec{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
        z-index: 200;

        .login_icon_btn{
          display: none;
          width: 38px;
          img{
            width: 100%;
          }
        }
        
        .search_btn{
          display: inline-flex;
          align-items: center;
          justify-content: center;
          padding: 12px 15px;
          border-radius: 8px;
          background-color: $color-light-grey;
          margin-left: 10px;
          img{
            width: 15px;
          }
        }

        .search_box{
          display: flex;
          position: absolute;          
          right: 0;
          background-color: $color-light-grey;
          border-radius: 8px;          
          align-items: center;
          justify-content: space-between;
          padding: 15px;
          opacity: 0;
          transition: 0.5s ease all;
          width: 0;
          z-index: -1;
          input[type=text]{
            display: block;
            width: 100%;
            background-color: transparent;            
            font-size: 1.6rem;
          }
          a{
            display: inline-block;
            width: 15px;
            img{
              width: 100%;
            }
          }
          &.show{
            width: auto;
            left: 0;
            z-index: 10;
            opacity: 1;
            overflow: visible;
            transition: 0.5s ease all;
          }
        }
      }      

      .hamburger{
        display: none;
        width: 45px;
        height: 30px;
        position: relative;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .5s ease-in-out;
        -moz-transition: .5s ease-in-out;
        -o-transition: .5s ease-in-out;
        transition: .5s ease-in-out;
        cursor: pointer;
        span{
          display: block;
          position: absolute;
          height: 4px;
          width: 100%;
          background: $primary-color;
          border-radius: 20px;
          opacity: 1;
          left: 0;
          -webkit-transform: rotate(0deg);
          -moz-transform: rotate(0deg);
          -o-transform: rotate(0deg);
          transform: rotate(0deg);
          -webkit-transition: .25s ease-in-out;
          -moz-transition: .25s ease-in-out;
          -o-transition: .25s ease-in-out;
          transition: .25s ease-in-out;
          -webkit-transform-origin: left center;
          -moz-transform-origin: left center;
          -o-transform-origin: left center;
          transform-origin: left center;
          &:nth-child(1){
            top: 0px;            
          }
          &:nth-child(2){
            top: 10px;
            width: 60%;            
          }
          &:nth-child(3){
            bottom: 0;            
          }
        }
        &.open{
          span{
            &:nth-child(1){
              -webkit-transform: rotate(45deg);
              -moz-transform: rotate(45deg);
              -o-transform: rotate(45deg);
              transform: rotate(45deg);
              top: 0px;
              left: 8px;
            }
            &:nth-child(2) {
              width: 0%;
              opacity: 0;
            }            
            &:nth-child(3) {
              -webkit-transform: rotate(-45deg);
              -moz-transform: rotate(-45deg);
              -o-transform: rotate(-45deg);
              transform: rotate(-45deg);
              top: 22px;
              left: 8px;
              width: 100%;
            }
          }
        }
        &:hover{
          span{
            background: $primary-color-yellow;
          }
        }
      }

      .profile_head{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 22%;
        @media (max-width: $breakpoint-tablet){
          width: 35%;
        }
        @media (max-width: $breakpoint-mobile-big){
          width: 50%;
        }
        .profile_info{
          position: relative;
          a{
            &.profile_img{
              display: flex;
              align-items: center;
              position: relative;
              padding-right: 20px;
              img{
                width: 50px;
                @media (max-width: $breakpoint-mobile-big){
                  width: 30px;
                }
              }
              span{
                display: inline-block;
                font-size: 1.4rem;
                margin-left: 10px;
                @media (max-width: $breakpoint-mobile-big){
                  margin-left: 5px;
                }
              }

              &::after{
                content: "";
                background: url("../../images/downarrow.svg") no-repeat center right;
                background-size: 100%;
                width: 12px;
                height: 12px;
                position: absolute;
                right: 0;
                @media (max-width: $breakpoint-mobile-big){
                  right: 10px;
                }
              }
            }          
          }

          .profile_box{
            display: none;
            position: absolute;
            background-color: $color-white;
            width: 300px;
            left: -50%;
            right: 0;
            top: 72px;
            margin: auto;
            padding: 30px;
            border-radius: 5px;
            box-shadow: 0px 0px 50px #00000080;
            z-index: 500;
            @media (max-width: $breakpoint-tablet){
              top: 67px;
            }
            @media (max-width: $breakpoint-mobile-big){
              top: 48px;
              left: auto;
              right: -50%;
            }
            h4{
              font-size: 1.6rem;
              font-weight: lighter;
            }
            span{
              display: block;
              &.profile_tag{
                font-size: 1.4rem;
                margin-top: 20px;
              }                          
            }
            a{
              margin-top: 20px;
              &::after{
                content: none;
              }
            }
          }
        }

        .profile_icons{          
          display: flex;
          align-items: center;
          justify-content: flex-end;
          position: relative;
          a{
            &.main_btn{
              display: inline-block;
              margin-left: 20px;
              width: 25px;
              @media (max-width: $breakpoint-mobile-big){
                width: 20px;
                margin-left: 10px;
              }
              &:first-child{
                margin-left: 0;
              }
              img{
                width: 100%;
              }
            }          
          }

          .notification_box{
            display: none;
            position: absolute;
            background-color: $color-white;
            width: 300px;            
            right: 0;
            top: 60px;
            margin: auto;
            padding: 30px;
            border-radius: 5px;
            box-shadow: 0px 0px 50px #00000080;
            z-index: 500;
            @media (max-width: $breakpoint-tablet){
              top: 57px;
            }
            h4{
              font-size: 1.6rem;
              font-weight: lighter;
            }
            ul{
              margin-top: 20px;
              height: 350px;
              overflow: auto;
              scrollbar-width: thin;
              scrollbar-color: $primary-color $color-grey;
              &::-webkit-scrollbar {
                width: 6px;
              }   
              &::-webkit-scrollbar-track {
                  background-color: $color-grey;                    
                  border-radius: 10px;
              }                                
              &::-webkit-scrollbar-thumb {
                  background: $primary-color;
                  border-radius: 10px;
              }
              li{
                margin-bottom: 20px;
                a{
                  display: block;
                  width: 100%;
                  font-size: 1.4rem;
                  line-height: 25px;
                  span{
                    display: block;
                    color: $primary-color-yellow;
                    font-size: 1.2rem;
                  }
                }
                &:last-child{
                  margin-bottom: 0;
                }
              }
            }
          }    
          
          .commonPopup{
            min-width: 60%;
            border-radius: 5px;
            .setting_sec{
              h3{
                font-size: 1.6rem;
                font-weight: lighter;
              }

              h4{
                font-size: 1.4rem;                
              }

              .auction_notifi{
                margin-top: 30px;
                h4{
                  font-weight: bold;
                }

                .auction_notifi_cont{
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  margin-top: 10px;
                  p{
                    font-size: 1.4rem;                    
                  }
                }
              }              

              .check_cont{
                padding: 0;
                border: none;
                margin: 0;              
                ul{
                  margin-top: 20px;
                  display: flex;
                  align-items: center;
                  @media (max-width: $breakpoint-tablet){
                    flex-wrap: wrap;                    
                  }
                  li{
                    margin-right: 30px;
                    margin-bottom: 0;
                    @media (max-width: $breakpoint-tablet){
                      margin-right: 0px;
                      width: 33%;
                      margin-bottom: 20px;
                    }
                    @media (max-width: $breakpoint-mobile-big){
                      width: 50%;
                    }
                    &:last-child{
                      margin-right: 0;
                    }
                  }
                }                
              }

              .auction_email{
                margin-top: 50px;
                @media (max-width: $breakpoint-mobile-big){
                  margin-top: 30px;
                }
                a{
                  margin-top: 15px;
                }
              }

              .auction_permission{
                margin-top: 50px;
                @media (max-width: $breakpoint-mobile-big){
                  margin-top: 30px;
                }
                .check_cont{
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  ul{
                    li{
                      @media (max-width: $breakpoint-tablet){
                        margin-bottom: 0;
                      }
                      @media (max-width: $breakpoint-mobile-big){
                        width: auto;
                        margin-right: 20px;
                      }
                    }
                  }
                  a{
                    margin-top: 20px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}