.subscribe_container{
    background: url("../images/newsletter.jpg") no-repeat center;
    background-size: cover;
    padding: 70px 30px;    
    .subscribe_content{
        text-align: center;
        color: $color-white;
        h4{
            font-size: 4rem;
            font-weight: lighter;
        }
        p{
            color: $color-white;
            font-size: 1.6rem;            
            line-height: 30px;
            width: 60%;
            margin: auto;
            margin-top: 20px;
        }

        form{            
            width: 60%;
            margin: auto;
            margin-top: 50px;
            ul{
                display: flex;
                align-items: flex-end;
                justify-content: center;
                li{ 
                    width: 30%;
                    margin: 0 15px;
                    text-align: left;
                    display: flex;
                    align-items: flex-end;
                    padding-bottom: 10px;
                    border-bottom: 1px $color-white solid;
                    img{
                        width: 20px;
                    }
                    input[type=text]{
                        background-color: transparent;
                        font-size: 1.6rem;
                        display: block;
                        width: 100%;
                        color: $color-white;  
                        margin-left: 10px;
                        &::placeholder{
                            color: $color-white;
                        }
                        &:focus{
                            &::placeholder{
                                color: transparent;
                            }
                        }
                    }
                    input[type=submit]{
                        padding: 15px 40px;
                    }
                    &:last-child{
                        width: auto;
                        border-bottom: none;
                        padding-bottom: 0;
                    }
                }
            }
        }
    }
}