.reachus_cont{
    margin-top: 20px;
    .container{
        display: flex;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            display: block;
        }
        .left_sec{
            width: 50%;
            font-size: 0;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }
            img{
                width: 100%;
                max-height: 580px;     
                @media (max-width: $breakpoint-tablet){
                    max-height: none;
                    height: 85vh;
                    object-fit: cover;
                }
                @media (max-width: $breakpoint-mobile-big){
                    height: auto;
                }
            }
        }

        .right_sec{
            width: 50%;
            background-color: $primary-color;
            padding: 50px;
            padding-bottom: 0;
            position: relative;
            @media (max-width: $breakpoint-tablet){
                padding: 25px;
            }
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }
            p{
                color: $color-white;
            }
            form{
                margin-top: 50px;
                @media (max-width: $breakpoint-tablet){
                    margin-top: 25px;
                }
                ul{
                    li{
                        margin-bottom: 40px;
                        &.mb_15{
                            margin-bottom: 15px;
                        }
                    }
                }                
            }

            .reachus_info{
                display: flex;                
                background-color: #363636;                
                margin-top: 50px;
                padding: 30px 50px;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                width: 100%;
                @media (max-width: $breakpoint-tablet){
                    padding: 25px;
                    justify-content: space-between;
                }
                @media (max-width: $breakpoint-mobile-big){
                    position: static;
                    display: block;                    
                }
                div{
                    &.phone_info{
                        display: flex;
                        align-items: center;
                        margin-right: 100px;
                        @media (max-width: $breakpoint-tablet){
                            display: block;
                            margin-right: 0;                            
                            text-align: center;
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            margin-right: 0;
                            display: flex;
                            align-items: center;   
                            margin-bottom: 20px;
                        }
                    }
                    &.email_info{
                        display: flex;
                        align-items: center;
                        @media (max-width: $breakpoint-tablet){
                            display: block;
                            text-align: center;
                        }
                    }
                    img{
                        width: 50px;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 30px;
                        }
                    }
                    span{
                        display: block;
                        font-size: 1.8rem;
                        color: $primary-color-yellow;
                        margin-left: 15px;                        
                        @media (max-width: $breakpoint-tablet){
                            font-size: 1.4rem;
                            display: block;
                            margin-top: 10px;
                            margin-left: 0;
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            margin-left: 20px;
                            margin-top: 0;
                        }
                    }
                }
            }
        }
    }
}

.map_sec{
    margin-top: 30px;
    position: relative;
    font-size: 0;
    margin-bottom: 70px;
    img{
        width: 100%;
        max-height: 600px;
        object-fit: cover;
        @media (max-width: $breakpoint-tablet){
            max-height: none;
            height: 75vh;
            object-fit: cover;
        }
        @media (max-width: $breakpoint-mobile-big){
            height: auto;
        }
    }

    .map_info{
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 30%;
        padding: 50px;
        border-radius: 5px;
        background-color: $color-white;
        -webkit-box-shadow: -5px 0px 40px 0px rgba(204,204,204,1);
        -moz-box-shadow: -5px 0px 40px 0px rgba(204,204,204,1);
        box-shadow: -5px 0px 40px 0px rgba(204,204,204,1);
        @media (max-width: $breakpoint-tablet){
            padding: 25px;
            width: 40%;
        }
        @media (max-width: $breakpoint-mobile-big){
            position: static;
            width: 100%;
        }
        .headoffice{
            img{
                width: 15%;
                height: auto;
            }
            h5{
                font-size: 1.8rem;
                font-weight: bold;
                margin: 20px 0;
            }            
            &.mt_50{
                margin-top: 70px;
                @media (max-width: $breakpoint-tablet){
                    margin-top: 50px;
                }
            }
        }
    }
}