.services_slider_container{
    background-color: $color-light-grey;    
    margin-top: 30px;
    position: relative;
    padding-bottom: 50px;
    .container{
        overflow: hidden;    
        .services_slider{
            padding: 50px 0px;
            .swiper-wrapper{
                .item{
                    background-color: $primary-color;
                    border-radius: 10px;
                    text-align: center;
                    padding: 30px 0;
                    padding-top: 0;
                    transition: 0.3s;                
                    .services_content{
                        position: relative;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        span{
                            display: block;
                            color: $color-white;
                            font-size: 12rem;
                            opacity: 0.1;
                        }
                        h5{
                            color: $color-white;
                            font-size: 2.5rem;
                            line-height: 30px;
                            font-weight: lighter;
                            position: absolute;                        
                            left: 0;
                            right: 0;
                            margin: auto;
                            width: 60%;
                            margin-top: 15px;
                        }
                    }
                    img{
                        width: 100%;
                        margin-left: 30px;
                        max-height: 170px;
                        object-fit: cover;
                        border-radius: 5px;
                    }
                    &:hover, &.swiper-slide-next{
                        transform: scale(1.1);
                        .services_content{
                            h5{
                                color: $primary-color-yellow;
                            }
                        }                    
                    }
                }
            }
        }
    }

    .services_btn{
        position: absolute;
        bottom: -18px;
        left: 0;
        right: 0;
        margin: auto;
        text-align: center;        
    }
}