.auction_main_tabs{
    border-bottom: 1px #b7b7b7 solid;
    .container{
        ul{
            display: flex;
            -ms-overflow-style: none;
            overflow: -moz-scrollbars-none;
            &::-webkit-scrollbar {
                display: none;
            }
            li{
                a{
                    display: block;
                    font-size: 2.5rem;
                    padding: 0 20px 10px 20px;
                    position: relative; 
                    &.active, &:hover{
                        font-weight: bold;
                        &::after{
                            content: "";
                            height: 3px;
                            width: 100%;
                            background-color: $primary-color-yellow;
                            position: absolute;
                            left: 0;
                            right: 0;
                            bottom: -1.5px;
                        }
                    }     
                    @media (max-width: $breakpoint-mobile-big){
                        font-size: 2rem;
                    }                              
                }
                @media (max-width: $breakpoint-mobile-big){
                    flex-grow: 1;
                    flex-shrink: 0;
                }
            }
            @media (max-width: $breakpoint-mobile-big){
                overflow-x: auto;
            }
        }
    }
}

.auction_main_content{
    margin-top: 50px;
    @media (max-width: $breakpoint-mobile-big){
        margin-top: 30px;
    }
    .container{
        display: flex;
        justify-content: space-between;
        .left_sec{
            width: 20%;
            background-color: $color-white;
            box-shadow: 0px 20px 50px #0000001F; 
            padding-bottom: 70px;
            @media (max-width: $breakpoint-tablet){
                width: 35%;
            }  
            @media (max-width: $breakpoint-mobile-big){
                display: none;
                position: fixed;
                left: 0;
                right: 0;
                top: 0;
                width: 100%;
                height: 100vh;
                overflow: auto;
                z-index: 1000;
                background-color: $color-light-grey;
            }
        }

        .right_sec{
            width: 80%;
            padding: 0 50px;
            position: relative;
            @media (max-width: $breakpoint-tablet){
                width: 65%;
                padding: 0 25px;
            }
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                padding: 0;
            }
            .search_grid{
                display: flex;
                align-items: center;
                justify-content: space-between;
                @media (max-width: $breakpoint-mobile-big){
                    display: block;
                }
                .search_wrap{
                    width: 50%;
                    padding: 0 15px 10px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    border-bottom: 1px #b7b7b7 solid;  
                    @media (max-width: $breakpoint-mobile-big){
                        width: 100%;
                    }                                     
                    input{
                        display: inline-block;
                        font-style: italic;
                    }
                    a{
                        display: inline-block;
                        width: 15px;
                        img{
                            width: 100%;
                        }
                    }
                }
                .grid_icons{
                    display: flex;
                    align-items: center;
                    @media (max-width: $breakpoint-mobile-big){
                        margin-top: 25px;
                        justify-content: space-between;
                    }
                    .filter_icon{
                        display: none;
                        a{
                            display: inline-block;
                            width: 40px;
                            height: 40px;
                            padding: 10px;
                            border-radius: 50%;
                            margin-left: 0;
                            opacity: 1;
                            background-color: $color-light-grey;
                            img{
                                width: 100%;
                            }
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            display: inline-block;
                        }
                    }

                    .filter_grid{   
                        display: flex;
                        align-items: center;                 
                        a{
                            display: inline-block;
                            width: 30px;
                            margin-left: 20px;
                            opacity: 0.3;
                            @media (max-width: $breakpoint-tablet){
                                margin-left: 15px;
                                width: 20px;
                            }
                            @media (max-width: $breakpoint-mobile-big){
                                width: 25px;
                                margin-left: 15px;
                            }
                            &:first-child{
                                margin-left: 0;
                            }
                            img{
                                width: 100%;
                            }
                            &.active{
                                opacity: 1;
                            }
                            &.print_btn{
                                width: 40px;
                                @media (max-width: $breakpoint-tablet){
                                    width: 30px;
                                }
                                @media (max-width: $breakpoint-mobile-big){
                                    width: 35px;
                                }
                            }
                        }
                    }
                }
            }            

            .result_msg{
                position: absolute;
                right: 50px;
                top: 70px;
                @media (max-width: $breakpoint-tablet){
                    right: 25px;
                }
                @media (max-width: $breakpoint-mobile-big){
                    position: static;                
                    padding-top: 15px;                    
                }
                span{
                    display: inline-block;
                    font-size: 1.6rem;
                    font-style: italic;
                }            
            }
            
            .news_tabs{
                margin-top: 30px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                position: relative;                
            }

            .auction_content{
                margin-top: 15px;
                ul{
                    display: flex;                    
                    flex-wrap: wrap;
                    @media (max-width: $breakpoint-mobile-big){
                        margin-top: 20px;
                    }
                    li{
                        width: 33%;
                        padding: 15px;
                        padding-bottom: 50px;
                        margin-bottom: 50px;
                        border-bottom: 1px #c7c7c7 solid;  
                        margin-right: 4px;
                        &:nth-child(3n){
                            margin-right: 0;
                        }
                        @media (max-width: $breakpoint-tablet){
                            width: 48%;
                            margin-bottom: 30px;
                            padding: 0;
                            padding-bottom: 30px;
                            margin-right: 16px !important;
                            &:nth-child(2n){
                                margin-right: 0 !important;
                            }                        
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            width: 100%;
                            margin-right: 0;
                            &:nth-child(3n){
                                margin-right: 0px;
                            }
                        }
                        &:hover{
                            box-shadow: 0px 20px 50px #0000001F;
                        }                                                                  
                        

                        .left{
                            display: flex;
                            flex-flow: column;
                            .auction_announce{
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                span{
                                    font-size: 1.4rem;
                                    font-weight: bold;
                                    margin-right: 30px;
                                }
                                img{
                                    width: 20px;                                
                                }
                            }

                            .highlight_tools{
                                justify-content: flex-end;
                            }
            
                            .auction_img{
                                margin-top: 10px;                            
                                a{
                                    img{
                                        width: 100%;  
                                        max-height: 280px;
                                        object-fit: contain;                                  
                                    }                                
                                }                            
                            }
                        }  
                        
                        .right{
                            .auction_info{
                                margin-top: 15px;
                                h4{
                                    font-size: 1.8rem;
                                    font-weight: lighter;                                
                                    line-height: 25px;
                                    @media (max-width: $breakpoint-tablet){
                                        font-size: 1.6rem;
                                        line-height: 20px;
                                    }
                                    span{
                                        display: block;
                                        font-size: 1.4rem;
                                    }
                                }
                                span{
                                    display: none;
                                }

                                a{
                                    &.auction_lot{
                                        background-color: #EFEFEF;
                                        font-size: 1.2rem;
                                        display: inline-block;
                                        padding: 5px 20px;
                                        border-radius: 15px;
                                        line-height: normal;
                                        text-transform: uppercase;
                                        align-self: flex-start;
                                        transition: 0.3s;
                                        &:hover{
                                            background-color: $primary-color-yellow;
                                        }
                                    }
                                }

                                .bids_no{
                                    span{
                                        display: block;
                                        text-align: right;
                                        font-size: 1.4rem;
                                        &:last-child{
                                            font-weight: bold;
                                        }
                                    }
                                }

                                &.lot_sec{
                                    display: flex;
                                    justify-content: space-between;
                                    h4{
                                        width: 70%;
                                    }
                                    a{
                                        align-self: flex-start;
                                    }
                                }
                            }

                            .auction_live{
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                                height: 50px;
                                @media (max-width: $breakpoint-tablet){
                                    display: block;
                                    height: auto;
                                    margin-top: 15px;
                                }
                                @media (max-width: $breakpoint-mobile-big){
                                    display: flex;
                                }
                                span{
                                    display: inline-block;
                                    font-size: 1.4rem;
                                    font-weight: bold;
                                    @media (max-width: $breakpoint-tablet){
                                        margin-bottom: 10px;
                                    }
                                    @media (max-width: $breakpoint-mobile-big){
                                        margin-bottom: 0;
                                    }
                                }
                            }

                            .auction_estimate{
                                margin: 20px 0;
                                span{
                                    display: block;
                                    font-size: 1.4rem;
                                }
                                p{
                                    font-size: 1.6rem;
                                    font-weight: bold;
                                    margin-top: 5px;
                                }
                            }

                            .auction_sales{
                                margin-top: 20px;
                                margin-bottom: 20px;
                                span{
                                    display: block;
                                    font-size: 1.4rem;
                                }
                                h5{
                                    font-size: 2rem;
                                    font-weight: bold;
                                    margin-top: 10px;
                                }
                            }

                            .auction_cta_bid{
                                display: flex;
                                flex-direction: column;
                                .auction_cta{   
                                    margin-top: 15px;                         
                                    a{
                                        display: inline-block;
                                        padding: 10px 20px;
                                    }
                                }

                                .auction_bid{
                                    @media (max-width: $breakpoint-tablet){
                                        width: 100%;
                                        margin-top: 10px;
                                        padding: 5px 10px;
                                    }
                                }                                
                            } 
                        }                       
                    }

                    &.listview{
                        display: block;
                        li{
                            display: flex;
                            justify-content: space-between;
                            width: 100%;
                            padding-bottom: 25px;
                            margin-bottom: 30px;                            
                            @media (max-width: $breakpoint-mobile-big){
                                position: relative;
                                &.bid_yes{
                                    padding-bottom: 55px;
                                }
                            }
                            .left{
                                width: 25%;
                                @media (max-width: $breakpoint-tablet){
                                    width: 40%;
                                }    
                                @media (max-width: $breakpoint-mobile-big){
                                    width: 30%;
                                }           
                                .auction_announce{
                                    display: none;
                                    max-width: 60%;                                     
                                }

                                .highlight_tools{
                                    order: 2;
                                    margin-top: 10px;
                                    justify-content: center;
                                }
                            }
                            .right{
                                width: 70%;
                                position: relative;
                                @media (max-width: $breakpoint-tablet){
                                    width: 55%;
                                }
                                @media (max-width: $breakpoint-mobile-big){
                                    width: 67%;
                                    position: static;
                                }                                
                                .auction_info{
                                    display: flex;                                    
                                    justify-content: space-between;                                    
                                    @media (max-width: $breakpoint-tablet){
                                        margin-top: 10px;                                        
                                    }
                                    h4{
                                        margin-top: 0;
                                        max-width: 60%; 
                                        @media (max-width: $breakpoint-tablet){
                                            max-width: 80%; 
                                        }
                                        @media (max-width: $breakpoint-mobile-big){
                                            font-size: 1.4rem;
                                            line-height: 15px;
                                        }
                                    }
                                    span{
                                        display: inline-block;
                                        width: 20px;
                                        img{
                                            width: 100%;
                                        }
                                    }
                                }

                                .auction_live{
                                    @media (max-width: $breakpoint-tablet){
                                        display: flex;
                                        align-items: center;
                                    }
                                    span{
                                        margin-bottom: 0;                                        
                                    }
                                    @media (max-width: $breakpoint-mobile-big){
                                        img{
                                            width: 38%;
                                        }
                                    }
                                }

                                .highlight_bid{
                                    max-width: 40%;
                                }

                                .highlight_button{
                                    max-width: 45%;
                                }

                                .auction_cta_bid{
                                    flex-direction: row;
                                    justify-content: space-between;
                                    align-items: center;
                                    position: absolute;
                                    left: 0;
                                    right: 0;
                                    bottom: 0;
                                    @media (max-width: $breakpoint-tablet){
                                        display: block;
                                        position: static;
                                        margin-top: 20px;
                                    }
                                    .auction_cta{
                                        @media (max-width: $breakpoint-mobile-big){
                                            padding: 5px 15px;
                                        }
                                    }
                                    .auction_cta, .auction_bid{
                                        margin-top: 0;
                                        margin-bottom: 0;
                                    }
                                    .auction_bid{
                                        width: 45%;
                                        @media (max-width: $breakpoint-tablet){
                                            width: 100%;
                                            margin-top: 10px;
                                            padding: 5px 10px;
                                        }
                                        @media (max-width: $breakpoint-mobile-big){
                                            position: absolute;
                                            left: 0;
                                            right: 0;
                                            bottom: 0;
                                        }
                                    }
                                }
                            }                        
                        }
                        &.auction_result_list{
                            li{
                                .auction_cta_bid{
                                    margin-top: 15px;
                                    position: static;
                                }
                            }                        
                        }
                    }                    
                }                
            }

            .auctions_main_tabcontent{
                display: none;
                &.active{
                    display: block;
                }
            }
        }
    }    
    &.management_container{
        .result_msg{
            position: static !important;
            margin-top: 20px;
        }
        .management_team.artist_team{
            margin-top: 20px;
            ul{
                li{
                    padding-bottom: 30px;
                    width: 31%;
                    img{                        
                        @media (max-width: $breakpoint-tablet){
                            width: 75%;                            
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            width: 50%;
                        }
                    }
                }

                &.listview{
                    li{                        
                        align-items: center;
                        margin-top: 0;
                        margin-bottom: 30px;
                        img{
                            position: static;
                            width: 20%;
                            margin: 0;
                        }
                        .management_content{
                            width: 75%;
                            text-align: left;
                            h4{
                                margin-top: 0;                                
                            }
                        }
                    }
                }
            }        
        }
        .management_content{
            h4{
                margin-top: 20px;                
            }            
        }
    }
}

.price_dropdown{
    margin-top: 25px;
    text-align: right;
    select{
        width: 15%;
        font-size: 1.6rem;
        border-color: #b7b7b7;                    
        @media (max-width: $breakpoint-mobile-big){
            width: 40%;
        }
    }
}

.auction_bid{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $color-pink;
    padding: 5px 15px;
    border-radius: 5px;
    margin-top: 20px;
    @media (max-width: $breakpoint-tablet){
        padding: 10px;
    }
    @media (max-width: $breakpoint-mobile-big){
        padding: 5px 10px;
    }
    p{
        font-size: 1.2rem;
        font-weight: lighter;
    }
    .bid_sec{
        @media (max-width: $breakpoint-tablet){
            margin-top: 5px;                                    
        }
        p{
            font-size: 2rem;
            font-weight: bold;
            display: flex;
            align-items: center;
            @media (max-width: $breakpoint-tablet){
                font-size: 1.8rem;
            }
            span{
                display: inline-block;
            }
            &.bid_small{
                font-size: 1.2rem;
                font-weight: lighter;
                justify-content: space-around;   
                @media (max-width: $breakpoint-tablet){
                    justify-content: flex-start;
                    span{
                        margin-right: 3px;
                    }
                }                                     
            }
        }
    }
}