.news_container{
    margin-bottom: 70px;
    .container{
        .news_tabs{
            position: relative;
            &::after{
                content: "";
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                height: 2px;
                background-color: $primary-color-yellow;
            }
            ul{
                display: flex;
                overflow-x: auto;
                flex-wrap: inherit;
                -ms-overflow-style: none;
                overflow: -moz-scrollbars-none;
                &::-webkit-scrollbar {
                    display: none;
                }
                li{
                    margin-right: 2px;
                    @media (max-width: $breakpoint-tablet){
                        flex-grow: 1;
                        flex-shrink: 0;
                    }
                    &:last-child{
                        margin-right: 0;
                    }
                    a{
                        display: block;
                        font-size: 1.6rem;
                        background-color: $color-light-grey;
                        padding: 10px 20px;
                        border-top-left-radius: 5px;
                        border-top-right-radius: 5px;
                        &.active{
                            background-color: $primary-color-yellow;
                        }
                    }
                }
            }
        }

        .news_tab_content{        
            .news_content{
                margin-top: 30px;
                display: flex;
                justify-content: space-between;            
                .left_sec{
                    width: 65%;
                    .big_news{
                        display: flex;
                        justify-content: space-between;
                        .big_news_img{
                            width: 48%;
                            img{
                                width: 100%;
                            }
                        }
                        .big_news_content{
                            width: 48%;
                            .tag{
                                display: inline-block;
                                background-color: $primary-color;
                                color: $color-white;
                                font-size: 1.2rem;
                                text-transform: uppercase;
                                padding: 5px 30px;
                                border-radius: 50px;
                                margin-top: 20px;
                            }                        
                            h4{
                                font-size: 2rem;
                                color: $primary-color-yellow;
                                font-weight: lighter;
                                margin-top: 10px;
                                line-height: 25px;
                            }
                            p{
                                font-size: 1.4rem;
                                line-height: 25px;
                                margin-top: 20px;
                            }
                            a{
                                margin-top: 30px;
                            }
                        }
                    }
                }

                .right_sec{
                    width: 30%;
                    ul{
                        position: relative;
                        height: 100%;
                        display: flex;
                        justify-content: space-between;
                        flex-flow: column;
                        li{                  
                            margin-bottom: 10px;      
                            a{
                                display: flex;
                                align-items: center;
                                justify-content: space-between;                            
                                img{
                                    max-width: 120px;
                                    margin-right: 20px;
                                }
                                h5{
                                    font-size: 1.8rem;
                                    color: $primary-color-yellow;
                                    font-weight: lighter;
                                    margin-top: 10px;
                                    transition: 0.3s;
                                }
                                .date{
                                    margin-top: 0;
                                }
                                &:hover{
                                    h5{
                                        color: $primary-color;
                                    }
                                }
                            }
                        }
                    }
                }

                .date{
                    display: block;
                    font-size: 1.4rem;
                    margin-top: 20px;
                    font-weight: bold;
                    font-family: $font-default;
                }                
            }
            .news_btn{
                width: 100%;
                text-align: center;
                margin-top: 50px;
            }
        }
    }
    
    &.story_tab_container{
        margin-bottom: 50px;
    }

    &.videos_tab_container{
        background-color: $color-light-grey;
        margin-top: 20px;
        padding: 50px 0;
        margin-bottom: 0;
        .container{
            .news_tabs{
                &::after{
                    background-color: #ccc;
                }
                ul{
                    overflow: visible;
                    li{
                        a{  
                            position: relative;                          
                            &.active, &:hover{                                
                                background-color: transparent;
                                font-weight: bold;                                
                                &::after{
                                    content: "";
                                    width: 100%;
                                    height: 4px;
                                    background-color: $primary-color-yellow;
                                    position: absolute;
                                    left: 0;
                                    right: 0;
                                    bottom: -1px;
                                    z-index: 10;
                                }
                            }
                        }
                    }
                }
            }
        }
        .stories_tabcontent{
            .story_slider{
                .item{                
                    .story_content{
                        background-color: $color-white;
                    }
                    &:hover{
                        .story_content{
                            transition: 0.3s;
                            background-color: $primary-color-yellow;
                        }                        
                    }
                }
            }
        }
    }
}