.sell_steps_cont{
    background-color: $color-light-grey;
    padding: 50px 0;

    .steps_cont{
        margin-top: 50px;
        ul{
            display: flex;
            justify-content: space-between;
            @media (max-width: $breakpoint-mobile-big){
                display: block;
            }
            li{
                width: 30%;
                position: relative;
                @media (max-width: $breakpoint-mobile-big){
                    width: 100%;
                    margin-bottom: 50px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
                span{
                    display: block;
                    text-align: center;
                    font-size: 6rem;
                    color: $primary-color-yellow;
                    font-weight: bold;
                    position: absolute;
                    width: 100%;
                    z-index: 10;
                    top: -40px;
                    &.line{
                        display: inline-block;
                        width: 2px;
                        background-color: $primary-color;
                        height: 60px;
                        position: absolute;
                        left: 50px;
                        bottom: 55px;
                        top: auto;
                        @media (max-width: $breakpoint-tablet){
                            bottom: 22%;
                            left: 30px;
                            height: 50px;
                        }
                    }
                }
                .step_image{
                    overflow: hidden;
                    border-radius: 10px;
                    img{
                        width: 100%;                        
                        -webkit-filter: grayscale(100%);
                        filter: grayscale(100%);
                        transition: 1s;
                        max-height: 450px;
                        object-fit: cover;
                        @media (max-width: $breakpoint-mobile-big){
                            max-height: 350px;
                        }
                        &:hover{
                            -webkit-filter: grayscale(0%);
                            filter: grayscale(0%);
                            transform: scale(1.1);
                        }
                    }
                }                
                p{
                    font-size: 1.4rem;
                    line-height: 25px;                    
                    margin: auto;
                    margin: 30px 0px 0 40px;  
                    @media (max-width: $breakpoint-tablet){
                        margin: 25px 0 0 25px;
                    }  
                    br{
                        @media (max-width: $breakpoint-tablet){
                            display: none;
                        }
                    }                
                }
            }
        }
    }
}

.workenv_container{    
    &.effort_sell{
        padding-top: 0;
        @media (max-width: $breakpoint-tablet){
            padding-top: 50px;
            margin-top: 0;
        }
        .container{
            align-items: center;
        }
        &::before{
            content: "";
            width: 100%;
            background-color: #fff;
            height: 70px;
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            @media (max-width: $breakpoint-tablet){
                display: none;
            }            
        }
    }
}

.faq_sec{
    background-color: $color-light-grey;
    padding: 50px 0;
    .faq_accordion{
        li{
            background-color: $color-white;
            margin-bottom: 20px;
            a{
                font-size: 2rem;                
                padding: 20px;
                position: relative;
                display: flex;
                align-items: center;
                padding-left: 70px;
                &::before{
                    content: "+";
                    font-size: 3rem;
                    background-color: $primary-color-yellow;
                    padding: 0 10px;
                    position: absolute;
                    left: 20px;                                        
                }
                &.active{
                    &::before{
                        content: "-";
                    }
                }
            }
            .toggle_inner{
                display: none;
                padding: 15px;
                font-size: 1.6rem;
                line-height: 30px;
                border-top: 1px #eee solid;
                padding-left: 20px;
            }
        }
    }
}

.sell_consign{
    margin-bottom: 70px;
    .auction_boxes{
        &.right_box{
            .right_sec{
                text-align: left;
                h4{
                    font-size: 3rem;
                    font-weight: lighter;
                }
                p{
                    margin-top: 20px;
                }
                a{
                    margin-top: 30px;
                }
            }            
        }
    }    
}