.highlight_container{
    .highlight_slider_container{    
        .highlight_slider{
            padding: 30px 0;
            overflow: hidden;             
        }
        .swiper-wrapper{        
            .item{
                text-align: center;
                padding: 30px;
                transition: 0.3s;
                border-right: 1px #ccc solid;
                &:hover, &.swiper-slide-next{
                    box-shadow: 0px 0px 20px 15px rgba(238,238,238,1);
                    border-right: none;
                }

                img{
                    max-width: 100%;
                    height: 200px;
                    object-fit: cover;
                    margin-top: 25px;
                }

                .highlight_detail{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    .highlight_name{
                        margin-top: 20px;
                        h4{
                            font-size: 2rem;
                            color: $primary-color-yellow;
                            font-weight: normal;
                            margin-top: 0;
                            margin-bottom: 0;
                        }
                        span{
                            display: block;
                            color: $primary-color;
                            font-size: 1.4rem;
                            margin-top: 10px;
                        }
                    }
                    .highlight_lot{
                        span{
                            background-color: $color-grey;
                            font-size: 1.2rem;
                            display: inline-block;
                            padding: 5px 20px;
                            border-radius: 15px;
                            line-height: normal;
                            text-transform: uppercase;                        
                        }
                    }

                }                                

                .highlight_time{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    border-radius: 10px;
                    margin-top: 20px;
                    background-color: $color-pink;
                    padding: 10px 15px;
                    .bid_time{
                        p{
                            font-size: 1.2rem;                        
                        }
                    }
                    .bid_clock{
                        p{
                            font-weight: bold;
                        }
                    }
                }
            }
        }
    }

    &.pastlot_container{
        background-color: $color-light-grey;
        padding: 20px 0;
        @media (max-width: $breakpoint-tablet){
            margin-top: 30px;
        }
        .highlight_slider_container{
            .pastlot_slider{
                padding: 30px 0;
                overflow: hidden;
                .swiper-wrapper{
                    .item{
                        border-right: none;
                        .highlight_tools{
                            justify-content: flex-end;
                        }
                        .highlight_detail{
                            .highlight_name{
                                h4{
                                    color: $primary-color;
                                }
                                span{
                                    margin-top: 10px;
                                }
                            }
                        }
                        .highlight_bid{
                            h4{
                                text-align: left;
                            }
                            span{
                                &.inclusive{
                                    font-style: italic;
                                    margin-top: 10px;
                                    font-size: 1.2rem;
                                    color: $color-dark-grey;
                                }
                            }
                        }
                    }
                }
            }
        }        
    }

    &.video_hig_cont{
        .heading_inner{
            .global_heading{
                @media (max-width: $breakpoint-mobile-big){
                    width: 30%;
                }                
                .auction_nav{
                    display: flex;
                }
            }
        }
    }
}

.highlight_tools{
    display: flex;
    align-items: center;
    justify-content: space-between;
    a{
        display: inline-block;
        width: 30px;
        svg{
            width: 100%;                            
        }
        img{
            width: 100%;
            height: auto !important;
            margin-top: 0 !important;
        }
    }
    .highlight_3d{
        a{                    
            display: flex;
            align-items: center;
            span{
                font-size: 1.2rem;
                font-weight: bold;
                padding-left: 5px;
                text-decoration: underline;
            }
        }
    }

    .highlight_like{
        a{
            &:first-child{
                margin-right: 10px;
            }
        }
    }

    .highlight_lot{
        span{
            background-color: #EFEFEF;
            font-size: 1.2rem;
            display: inline-block;
            padding: 5px 20px;
            border-radius: 15px;
            line-height: normal;
            text-transform: uppercase;
        }
    }

    .highlight_expand{
        display: flex;
        align-items: center;
        a{
            display: inline-flex;
            align-items: center;
            justify-content: center;
            background-color: $color-grey;
            width: 35px;
            height: 35px;
            border-radius: 50%;            
            padding: 12px;
            margin-left: 10px;
            transition: 0.3s;
            svg{
                width: 100%;
            }
            &:hover{
                background-color: $primary-color-yellow;
            }
        }
    }
}

.hightlight_img{
    position: relative;
    margin-top: 15px;
    .highlight_info_sec{                
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: $primary-color;
        display: none;
        flex-flow: column;
        justify-content: center;
        padding: 0 40px;
        transition: 0.3s;
        .highlight_info{
            text-align: left;
            margin-bottom: 20px;
            &:last-child{
                margin-bottom: 0;
            }
            span{
                display: block;
                color: $color-white;
                font-size: 1.4rem;            
            }
            h6{
                color: $primary-color-yellow;
                font-size: 1.6rem;
                font-weight: lighter;
                margin-top: 10px;
            }
        }
        .col_2{
            display: flex;
            justify-content: space-between;            
        }
    }
    &:hover{
        .highlight_info_sec{
            display: flex;            
        }
    }
}

.highlight_bid{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;                
    span{
        display: block;
        text-align: left;
        font-size: 1.4rem;
        @media (max-width: $breakpoint-mobile-big){
            margin-top: 5px;
        }
    }
    h4{
        font-size: 2rem;
        margin-top: 10px;
        @media (max-width: $breakpoint-tablet){
            font-size: 1.8rem;
        }
    } 
    .green_bid{
        h4{
            color: $color-green;
        }
    }

    &.size_bid{
        h4{
            font-size: 1.6rem;
        }
    }
}

.highlight_button{
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    a{
        display: inline-block;
        font-size: 1.4rem;
        text-transform: uppercase;   
        padding: 10px 30px;
        border-radius: 10px;
        font-weight: bold;
        @media (max-width: $breakpoint-tablet){
            padding: 10px 24px;
            font-size: 1.2rem;
        }
    }
}

.congrats_bid{    
    text-align: right;
    position: relative;
    padding-top: 30px;
    img{
        position: absolute;
        right: 0;
        top: -20px;
        width: 100px;
    }
    p{
        background-color: $color-green;
        color: $color-white;
        font-size: 1.4rem;
        padding: 5px 0 2px;
        border-radius: 5px;
        line-height: 20px;
        text-align: center;
    }
}

.bid_close{
    padding-top: 30px;
    p{
        background-color: $primary-color;
        color: $color-white;
        font-size: 1.4rem;
        padding: 5px 0 2px;
        border-radius: 5px;
        line-height: 20px;
        text-align: center;
    }
}