.ourCollector_container{
    .commonContent{        
        margin-top: 15px;
        p {
            color: $primary-color;
            font-size: 1.6rem;
            line-height: 30px;
            font-family: $font-regular;
            margin-top: 0;
        }
    }
    .rightSliderNav{
        margin-top: 10px;
        justify-content: flex-end;
    }

    .ourCollector_slider_container{
        margin-top: 30px;
        overflow: hidden;
        .swiper-wrapper{         
            .item{
                border: 7px solid $color-white;
                transition: 0.5s ease all;
                &:hover, &.swiper-slide-next{
                    border: 7px solid $primary-color;
                    .collectorProfile{
                        opacity:1;
                    }
                }
                img {
                    width: 100%;
                }
            }
            .imgSec{
                position: relative;
                .playBtn{
                    display: block;
                    width: 60px;
                    height:60px;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            .collectorProfile{
                display: flex;
                justify-content: space-between;
                align-items:center;
                padding: 10px 18px;
                transition: 0.5s ease all;
                opacity: 0;
                .profile{
                    display: flex;
                    align-items:center;
                    .nameDetail{
                        margin-left:15px;
                    }
                    h4{
                        color: $primary-color-yellow;
                        font-weight: normal;
                        font-family: $font-default;
                        font-size: 2rem;
                    }
                    span{
                        display: block;
                        color: $primary-color;                                                
                        font-size: 1.4rem;
                        margin-top: 5px;
                    }
                    .profilePic{
                        width: 70px;
                    }
                }
                .quoteImg{
                    background:url('/images/quote.svg') no-repeat 0 0;
                    width:55px;
                    height: 40px;
                    background-size: contain;
                }
            }
        }
    }

    .global-pagination{
        display: none;
    }
}

.commonPopup{
    position: fixed;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    z-index: 3000;
    width: auto;
    height: auto;
    padding: 20px;
    background:#fff;
    display: none;
    @media (max-width: $breakpoint-mobile-big){
        width: 90%;  
        padding: 10px;      
    }
    .closeBtn{
        position: absolute;
        right:-15px;
        top:-15px;
        background-color: #FFD018;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        cursor: pointer;
    }
    .content{
        height: 100%;
    }
    iframe{
        min-width: 700px;
        min-height: 350px;
        @media (max-width: $breakpoint-mobile-big){
            min-width: 280px;
            min-height: 140px;
        }
    }
    
    img{
        width: 100%;
        min-width: 280px;
        max-width: 600px;
    }
}

.collector_inner{
    margin-top: 50px;
    ul{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        li{
            width: 32%;
            margin-bottom: 50px;
            @media (max-width: $breakpoint-tablet){
                width: 48%;
            }
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                margin-bottom: 25px;
            }
            .coll_image{
                font-size: 0;
                position: relative;
                img{
                    width: 100%;
                }
                a{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    img{
                        width: 60px;
                    }
                }
            }
            .coll_content{
                display: flex;                
                justify-content: space-between;
                background-color: $color-light-grey;
                padding: 10px;
                .coll_info{
                    display: flex;
                    align-items: center;
                    img{
                        width: 80px;                        
                    }
                    .coll_name{
                        margin-left: 15px;
                        h5{
                            font-size: 1.8rem;
                            font-weight: lighter;
                        }
                        span{
                            display: block;
                            font-size: 1.4rem;
                            margin-top: 5px;
                        }
                    }                    
                }
                span{
                    width: 60px;
                    display: inline-block;
                    img{
                        width: 100%;
                    }
                }
            }
        }
    }
}