.auction_detail_banner{
    margin-top: 30px;
    .container{
        position: relative;
        font-size: 0;
        img{
            width: 100%;
            @media (max-width: $breakpoint-tablet){
                min-height: 300px;
                object-fit: cover;
            }
            @media (max-width: $breakpoint-mobile-big){
                min-height: 40vh;
            }
        }

        .auction_banner_content{
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            width: 100%;
            height: 100%;
            padding: 20px;
            display: flex;
            align-items: center;
            @media (max-width: $breakpoint-mobile-big){
                position: static;
                background-color: $primary-color;
            }
            @media (max-width: $breakpoint-mobile-big){
                display: block;
            }
            .download_cat{
                text-align: right;
                position: absolute;
                right: 20px;
                top: 20px;
                @media (max-width: $breakpoint-tablet){
                    right: 40px;
                }
                a{
                    display: inline-flex;
                    align-items: center;
                    img{
                        width: 35px;
                        @media (max-width: $breakpoint-tablet){
                            width: 25px;
                            min-height: auto;
                        }
                    }
                    span{
                        display: inline-block; 
                        font-size: 1.4rem;
                        color: $color-white; 
                        text-decoration: underline;
                        margin-left: 10px;                  
                    }                
                }
            }

            .auction_banner_info{
                text-align: center;
                width: 40%;
                margin: auto;    
                @media (max-width: $breakpoint-tablet){
                    width: 60%;
                }   
                @media (max-width: $breakpoint-mobile-big){
                    width: 100%;
                }                         
                h4{
                    font-size: 3rem;
                    line-height: 40px;
                    color: $primary-color-yellow;
                    font-weight: lighter;
                    @media (max-width: $breakpoint-tablet){
                        font-size: 2.2rem;
                        line-height: 25px;
                    }
                }
                span{
                    display: block;
                    color: $color-white;
                    font-size: 2.5rem;
                    font-weight: bold;
                    margin-top: 15px;
                    @media (max-width: $breakpoint-tablet){
                        font-size: 1.8rem;
                    }
                }
                div{                    
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 90%;
                    margin: auto;
                    margin-top: 20px;
                    @media (max-width: $breakpoint-tablet){
                        width: 85%;
                    }
                    @media (max-width: $breakpoint-mobile-big){
                        display: block;
                    }
                    a{
                        display: inline-flex;
                        align-items: center;
                        justify-content: center;
                        background-color: $color-light-grey;
                        text-transform: uppercase;
                        padding: 10px 15px;
                        border-radius: 10px;
                        @media (max-width: $breakpoint-mobile-big){
                            margin-bottom: 10px;
                        }                    
                        img{
                            width: 20px;
                            min-height: auto;
                        }
                        span{
                            font-size: 1.6rem;
                            font-weight: normal;
                            margin-top: 0;
                            color: $primary-color;  
                            margin-left: 10px;  
                            @media (max-width: $breakpoint-tablet){
                                font-size: 1.4rem;
                            }                        
                        }
                    }
                }                
            }

            .auction_bid{
                position: absolute;
                bottom: 20px;
                right: 20px;
                width: 20%;
                background-color: rgba(255,255,255,0.2);
                margin-top: 0;
                @media (max-width: $breakpoint-tablet){
                    right: 40px;
                    bottom: 20px;
                    padding: 5px 10px;
                }
                @media (max-width: $breakpoint-mobile-big){
                    position: static;
                    width: auto;
                }
                p, span{
                    color: $color-white;
                }            
            }
        }
    }
}

.auction_main_content{
    &.auction_detail_container{
        .container{
            .right_sec{
                .news_tabs{
                    margin-top: 0;
                    ul{
                        margin-top: 0;
                    }                    
                }

                .tab-content{
                    margin-top: 30px !important;
                }

                .price_dropdown{
                    margin-top: 0;
                    width: 15%;
                    @media (max-width: $breakpoint-mobile-big){
                        width: 100%;
                        margin-top: 20px;
                    }
                    select{
                        width: 100%;
                    }                
                }
            
                .result_msg{
                    margin-top: 20px;
                    position: static;
                }
            
                ul{
                    margin-top: 20px;
                }
            }    
        }        
    }
}

.auction_popup{    
    position: fixed;
    width: 600px;
    margin: auto;
    top: 10%;
    background-color: $color-white;
    padding: 15px;
    img{
        width: 100%;
    }
}

#closing_schedule{
    .who_banner{
        h3{
            margin-top: 0;
        }
    }
}

.common_table{
    margin-top: 50px;
    overflow-x: auto;
    table{    
        width: 100%;
        @media (max-width: $breakpoint-mobile-big){
            width: auto;
        }
        tr{
            &:nth-child(even){
                background-color: $table-even;
            }
            &:nth-child(odd){
                background-color: $table-odd;
            }
            th{
                font-size: 1.4rem;
                color: $color-white;
                background-color: $primary-color-yellow;
                padding: 10px;
                font-weight: lighter;
                text-align: center;
                span{
                    display: block;
                }
            }

            td{
                padding: 30px 10px;
                text-align: center;
                font-size: 1.4rem;
                @media (max-width: $breakpoint-mobile-big){
                    padding: 10px;
                }
            }
        }
    }
}
