.history_container{
    background-color: $color-light-grey;
    padding: 30px 0;
    margin-top: 30px;
    @media (max-width: $breakpoint-tablet){
        margin-top: 30px !important;
    }
    .container{
        display: flex;        
        .left_sec{
            width: 15%;
            @media (max-width: $breakpoint-tablet){
                display: none;                
            }
            ul{                
                background-color: $color-white;  
                height: 450px;
                overflow-y: auto;
                scrollbar-width: thin;
                scrollbar-color: $primary-color $color-grey;
                &::-webkit-scrollbar {
                    width: 6px;
                }   
                &::-webkit-scrollbar-track {
                    background-color: $color-grey;                    
                    border-radius: 10px;
                }                                
                &::-webkit-scrollbar-thumb {
                    background: $primary-color;
                    border-radius: 10px;
                }              
                li{
                    a{
                        display: block;
                        font-size: 1.4rem;
                        padding: 15px 25px;
                        border-bottom: 1px #ccc solid;
                        position: relative;
                        &.active{
                            &::before{
                                content: "";
                                width: 10px;
                                background-color: $primary-color-yellow;
                                height: 101%;
                                position: absolute;
                                left: 0;
                                top: 0;
                            }
                        }
                    }
                    &:last-child{
                        a{
                            border-bottom: none;
                        }
                    }
                }
            }
        }

        .right_sec{
            width: 65%;
            margin-left: 70px;        
            @media (max-width: $breakpoint-tablet){
                width: 100%;
                margin-left: 0;
            }    
            .global_heading{
                .auction_nav{
                    width: 20%;
                    margin: auto;
                    @media (max-width: $breakpoint-tablet){                        
                        width: 22% !important;
                    }
                    @media (max-width: $breakpoint-mobile-big){
                        display: flex;
                        width: 50% !important;
                    }
                    .historyYear_slider{
                        span{
                            display: block;
                            font-size: 2.5rem;
                            font-weight: bold;
                            text-align: center;
                        }                        
                    }
                }
            }            

            .history_tab_content{
                margin-top: 30px;
                .tab-content{
                    img{
                        width: 100%;
                    }
                    p{
                        text-align: center;
                        margin-top: 15px;
                    }
                }
            }
        }
    }
}