.who_banner_container{
    @media (max-width: $breakpoint-mobile-big){
        margin-top: 30px !important;
    }
}

.auctionguide_tabs{
    margin-top: 30px;    
}

.auctionguide_tabs_content{
    margin-top: 30px;
    p{
        margin-top: 20px;
    }
}

.auction_boxes{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    @media (max-width: $breakpoint-mobile-big){
        display: block;
        margin-top: 30px;
    }
    .left_sec{
        width: 50%;
        position: relative;
        padding: 30px 0;
        @media (max-width: $breakpoint-tablet){
            align-self: stretch;
        }
        @media (max-width: $breakpoint-mobile-big){
            width: 100%;
            padding: 0;
        }
        img{
            width: 100%;
        }        
        &::after{
            width: 12%;
            content: "";
            position: absolute;
            right: 0;
            top: 0;
            height: 100%;
            background-color: $color-light-grey;
            z-index: -1;
            @media (max-width: $breakpoint-mobile-big){
                content: none;
            }
        }
    }    
    .right_sec{
        align-self: stretch;
        width: 50%;
        padding: 60px 50px 50px 30px;
        background-color: $color-light-grey;
        @media (max-width: $breakpoint-tablet){
            padding: 30px;
        }
        @media (max-width: $breakpoint-mobile-big){
            width: 100%;
        }
        h4{
            font-size: 2rem;
        }
    }
    &.right_box{
        flex-flow: row-reverse;
        .left_sec{
            &::after{
                left: 0;
                right: auto;
            }
        }
        .right_sec{
            text-align: right;
            @media (max-width: $breakpoint-mobile-big){
                text-align: left;
            }
        }
    }
}