.dash_header{
    background-color: $primary-color;  
    &.fixed{
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        z-index: 1000;
    }  
    .profile_links{
        ul{
            display: flex;
            align-items: center;
            justify-content: space-between;
            overflow-x: auto;
            -ms-overflow-style: none;
            overflow: -moz-scrollbars-none;
            &::-webkit-scrollbar {
                display: none;
            }
            @media (max-width: $breakpoint-tablet){
                justify-content: flex-start;
            }
            li{
                @media (max-width: $breakpoint-tablet){
                    flex-grow: 1;
                    flex-shrink: 0;
                    margin-right: 50px;
                }
                @media (max-width: $breakpoint-mobile-big){
                    margin-right: 25px;
                }
                &:last-child{
                    margin-right: 0;
                }
                a{
                    display: block;
                    font-size: 1.6rem;
                    color: $color-white;
                    display: flex;
                    align-items: center;
                    padding: 20px 0;
                    position: relative;
                    transition: 0.3s;
                    @media (max-width: $breakpoint-mobile-big){
                        font-size: 1.6rem;
                    }
                    svg{
                        width: 25px;
                        height: 25px;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 20px;
                            height: 20px;
                        }
                    }
                    span{
                        display: inline-block;
                        padding-left: 10px;
                    }
                    &::after{
                        content: "";
                        width: 100%;
                        left: 0;
                        right: 0;
                        position: absolute;
                        bottom: 0px;
                        background-color: $primary-color;
                        height: 5px;
                    }

                    &.active, &:hover{
                        color: $primary-color-yellow;
                        svg{
                            path{
                                fill: $primary-color-yellow;
                            }
                        }
                        &::after{
                            background-color: $primary-color-yellow;
                        }
                    }
                }
            }
        }
    }
}

.dash_heading{
    padding-top: 50px;
    .container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        h3{
            font-size: 2.5rem;
            font-weight: lighter;
        }
        .profile_com{
            width: 15%;
            @media (max-width: $breakpoint-mobile-big){
                width: 30%;
            }
            .profile_progress{
                display: block;
                margin-top: 5px;
                text-align: center;
            }
        }
    }
    &.gallery_head{
        .container{
            display: block;
            p{
                margin-top: 20px;
                @media (max-width: $breakpoint-mobile-big){
                    line-height: 20px;
                }
            }
        }
    }
}

.dashboard_content{
    padding-top: 30px;
    .row{
        margin-top: 20px;
        display: flex;        
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            display: block;
        }
        .dash_overview{
            width: 35%;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }       
            h4{
                font-size: 2rem;
                font-weight: lighter;
                margin-top: 15px;
            }

            .dash_overview_inner{
                box-shadow: 0px 0px 20px #0000001A;
                border-radius: 5px;
                padding: 25px;
                margin-top: 25px;
                .overview_limit{
                    margin-top: 20px;
                    text-align: center;
                    ul{
                        display: flex;
                        justify-content: space-between;
                        @media (max-width: $breakpoint-tablet){
                            flex-flow: column;
                            align-items: flex-start;                        
                        }
                        li{
                            @media (max-width: $breakpoint-tablet){
                                margin-bottom: 10px;
                            }
                            span{
                                display: block;
                                font-size: 1.4rem;
                                font-style: italic;
                                @media (max-width: $breakpoint-tablet){
                                    font-size: 1.2rem;
                                }
                            }
                            h5{
                                font-size: 1.8rem;
                                margin-top: 10px;
                                @media (max-width: $breakpoint-tablet){
                                    font-size: 1.4rem;
                                    text-align: left;
                                }
                            }
                        }
                    }
                    a{
                        margin-top: 20px;
                    }
                }

                .overview_total{
                    margin-top: 20px;
                    border-top: 1px $color-grey solid;
                    padding-top: 20px;
                    p{
                        font-size: 1.4rem;
                        font-style: italic;
                        span{
                            font-weight: bold;
                        }
                    }
                }
            }

            .executive_sec{
                margin-top: 20px;
                box-shadow: 0px 0px 20px #0000001A;
                border-radius: 5px;
                padding: 25px;
                h5{
                    font-size: 1.6rem;
                    font-weight: lighter;
                    font-style: italic;
                }
                p{
                    font-size: 1.4rem;
                    margin-top: 10px;
                    span{
                        font-size: 1.2rem;
                    }
                }
                .executive_sec_inner{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    @media (max-width: $breakpoint-tablet){
                        flex-flow: column;
                        align-items: flex-start;
                    }
                    .left_sec{
                        width: 48%;
                        @media (max-width: $breakpoint-tablet){
                            width: 100%;
                            order: 2;
                            margin-top: 10px;
                        }
                        a{
                            display: flex;
                            align-items: center;
                            margin-bottom: 15px;
                            &:last-child{
                                margin-bottom: 0;
                            }
                            img{
                                width: 35px;
                            }
                            span{
                                display: inline-block;
                                font-size: 1.4rem;
                                margin-left: 15px;
                            }
                        }
                    }
                    .right_sec{
                        width: 30%;
                        @media (max-width: $breakpoint-tablet){
                            width: 40%;
                            margin-top: 15px;
                            text-align: left;
                        }
                        img{
                            width: 100%;
                        }
                    }
                }
            }
        }

        .last_bids{
            width: 35%;
            position: relative;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                margin-top: 30px;
            }
            .bid_tabs{
                background-color: $color-light-grey;
                border-radius: 10px;
                display: inline-block;
                padding: 5px;
                a{
                    display: inline-block;
                    font-size: 1.6rem;
                    padding: 8px 30px;
                    border-radius: 10px;
                    &.active{
                        background-color: $primary-color-yellow;
                    }
                    @media (max-width: $breakpoint-tablet){
                        padding: 8px 15px;
                    }
                }
            }

            ul{
                li{
                    box-shadow: 0px 0px 20px #0000001A;
                    border-radius: 5px;
                    padding: 10px;
                    margin-top: 12px; 
                    display: flex;
                    justify-content: space-between;
                    .left_sec{
                        display: flex;
                        align-items: center;
                        @media (max-width: $breakpoint-tablet){
                            flex-flow: column;
                            align-items: flex-start;
                        }
                        img{
                            width: 70px;
                            margin-right: 10px;
                            @media (max-width: $breakpoint-tablet){
                                margin-right: 0;
                                width: 50px;                                
                            }
                        }
                        div{
                            @media (max-width: $breakpoint-tablet){
                                margin-top: 10px;                                
                            }
                            span{
                                display: block;
                                font-size: 1.4rem;
                                font-style: italic;                            
                            }
                            h6{                                
                                font-weight: bold;
                                font-size: 1.4rem;
                                margin-top: 5px;                                
                            }
                        }
                    }

                    .right_sec{
                        display: flex;
                        flex-flow: column;
                        align-items: flex-end;
                        justify-content: space-between;
                        .record_lot{
                            background-color: $color-light-grey;
                            font-size: 1.2rem;
                            display: inline-block;
                            padding: 5px 20px;
                            text-transform: uppercase;
                            border-radius: 20px;                        
                        }
                        .leading_sec{
                            display: flex;
                            align-items: center;
                            img{
                                width: 20px;
                            }
                            span{
                                display: inline-block;
                                background-color: $color-green;
                                color: $color-white;
                                font-size: 1.2rem;
                                padding: 5px 20px;
                                border-radius: 5px;
                                margin-left: 15px;
                            }
                        }
                        .bid_btn{
                            @media (max-width: $breakpoint-tablet){
                                display: flex;
                                flex-flow: column;
                                align-items: flex-end;                                
                            }
                            a{
                                padding: 8px 15px;
                                &:last-child{
                                    margin-left: 10px;
                                    @media (max-width: $breakpoint-mobile-big){
                                        margin-left: 0;                                        
                                    }
                                }   
                                @media (max-width: $breakpoint-tablet){
                                    margin-top: 10px;
                                }                         
                            }
                        }
                    }
                }
            }

            .view_all{
                display: inline-block;
                font-size: 1.6rem;
                text-decoration: underline;
                position: absolute;
                right: 0;
                bottom: -40px;
                transition: 0.3s;
                &:hover{
                    color: $primary-color-yellow;
                }
                @media (max-width: $breakpoint-mobile-big){
                    position: static;
                    margin-top: 10px;
                }
            }
        }

        .auction_calendar{
            width: 25%;
            box-shadow: 0px 0px 20px #0000001A;
            border-radius: 5px;
            padding: 10px;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                margin-top: 30px;
            }
            h4{
                font-size: 2rem;
                font-weight: lighter;
                margin-top: 5px;
            }
            .calendar_img{
                img{
                    width: 100%;
                    margin-top: 10px;
                }
            }

            ul{                
                padding: 20px 15px;
                height: 200px;
                overflow-y: auto;
                scrollbar-width: thin;
                scrollbar-color: $primary-color $color-grey;
                &::-webkit-scrollbar {
                    width: 6px;
                }   
                &::-webkit-scrollbar-track {
                    background-color: $color-grey;                    
                    border-radius: 10px;
                }                                
                &::-webkit-scrollbar-thumb {
                    background: $primary-color;
                    border-radius: 10px;
                }
                li{
                    margin-bottom: 20px;                
                    &:last-child{
                        margin-bottom: 0;
                    }
                    span{
                        display: block;
                        font-size: 1.4rem;
                        font-style: italic;
                    }
                    h6{
                        font-size: 1.4rem;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
}

.graph_sec{
    .container{
        display: flex;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            flex-flow: column;
        }
        h4{
            font-size: 2rem;
            font-weight: lighter;            
        }
        img{
            width: 100%;
        }
        div{
            box-shadow: 0px 0px 20px #0000001A;
            border-radius: 5px;
            padding: 25px;
        }
        .biding_ratio{            
            width: 35%;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }
        }
        .items_purchased{
            width: 63%;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                margin-top: 20px;
            }
        }
    }
}

.tracking_sec{
    .container{
        display: flex;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            flex-flow: column;
        }
        .tracking_status{
            width: 70%;
            box-shadow: 0px 0px 20px #0000001A;
            border-radius: 5px;
            padding: 25px;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }
            .tracking_head{
                display: flex;
                justify-content: space-between;
                h4{
                    font-size: 2rem;
                    font-weight: lighter;                    
                }
                .tracking_days{
                    span{
                        display: block;
                        text-align: center;
                        font-size: 1.4rem;
                        &:first-child{
                            display: inline-block;
                            font-size: 2rem;
                            font-weight: bold;
                            background-color: $color-pink;
                            width: 40px;
                            height: 40px;
                            margin: auto;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-radius: 50%;
                        }
                    }
                }            
            }

            .tracking_data{
                display: flex;
                align-items: center;
                .left_sec{
                    width: 15%;
                    @media (max-width: $breakpoint-mobile-big){
                        width: 40%;
                    }
                    img{
                        width: 100%;
                    }
                }
                .right_sec{
                    margin-left: 15px;
                    div{
                        margin-bottom: 20px;
                        h5{
                            font-size: 1.6rem;
                        }
                        span{
                            display: block;
                            font-size: 1.4rem;
                            margin-top: 5px;
                        }
                    }
                }
            }

            .tracking_progress{
                margin-top: 30px;
                @media (max-width: $breakpoint-mobile-big){
                    display: flex;
                    height: 50vh;
                    justify-content: space-between;
                }
                .tracking_img{
                    text-align: center;
                    @media (max-width: $breakpoint-mobile-big){
                        display: flex;
                    }
                    img{
                        max-width: 90px;
                    }
                }
                .progress_bar{
                    margin-top: 15px;
                    @media (max-width: $breakpoint-mobile-big){
                        height: 50vh;
                        width: 7px;
                    }
                    .progress_inner{
                        border-top-right-radius: 0;
                        border-bottom-right-radius: 0;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 100%;
                            height: 50%;
                            border-radius: 0;
                        }
                        &::after{
                            content: "";
                            position: absolute;
                            width: 2px;
                            height: 25px;
                            top: -7px;
                            left: 100%;
                            background-color: $primary-color-yellow;   
                            @media (max-width: $breakpoint-mobile-big){
                                width: 25px;
                                height: 2px;
                                top: 100%;
                                left: -7px;
                            }                     
                        }
                    }
                }
                ul{
                    margin-top: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    @media (max-width: $breakpoint-mobile-big){
                        flex-flow: column;
                    }
                    li{
                        font-size: 1.4rem;
                    }
                }            
            }

            .tracking_note{
                font-size: 1.4rem;
                font-weight: bold;
                font-style: italic;
                margin-top: 30px;
                line-height: 20px;
            }
        }

        .download_app_sec{
            width: 28%;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                margin-top: 30px;
            }
            .download_app{            
                box-shadow: 0px 0px 20px #0000001A;
                border-radius: 5px;
                padding: 25px;
                h4{
                    font-size: 2rem;
                    font-weight: lighter;                    
                }
                ul{
                    li{
                        margin-top: 30px;
                        span{
                            display: block;
                            font-size: 1.4rem;
                            font-weight: bold;
                        }
                        a{
                            display: inline-block;
                            margin-top: 10px;
                        }
                    }
                }
            }
        }    
    }
}

.dash_banner{
    margin-top: 30px;
    @media (max-width: $breakpoint-tablet){
        margin-top: 15px;
    }
    img{
        width: 100%;
        @media (max-width: $breakpoint-tablet){
            height: 200px;
            object-fit: cover;
        }
    }
}

.liveauction_tabs{
    &.auction_main_content{    
        .container{
            position: relative;    
            display: block !important;    
            .result_msg{
                position: absolute;
                right: 0;
                z-index: 100;
                top: 10px;
                @media (max-width: $breakpoint-tablet){
                    position: static;
                    margin-bottom: 15px;
                    text-align: right;
                }
                a{
                    display: inline-block;
                    font-size: 1.4rem;
                    text-decoration: underline;
                    transition: 0.3s;
                    &:hover{
                        color: $primary-color-yellow;
                    }
                }
            }

            .right_sec{
                width: 100% !important;
                padding: 0 !important;  
                .auction_content {
                    ul{
                        li{
                            border-bottom: none;
                            border-radius: 5px;
                            margin-bottom: 30px;
                            @media (max-width: $breakpoint-mobile-big){
                                margin-right: 0 !important;
                            }
                            .left {
                                .highlight_tools{
                                    justify-content: space-between;
                                }
                                .auction_img{
                                    text-align: center;
                                    img{
                                        max-width: 100%;
                                        height: 200px;
                                        width: auto;
                                        object-fit: cover;                                        
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.profile_tabs{
    margin-top: 30px;
    .container{
        display: flex;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            flex-flow: column;
        }
        .left_sec{
            width: 37%;
            position: relative;
            @media (max-width: $breakpoint-tablet){
                width: 45%;
            }
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                order: 2;
                margin-top: 30px;
            }
            ul{
                display: flex;
                justify-content: space-between;
                li{
                    position: relative;
                    a{
                        display: block;      
                        text-align: center; 
                        opacity: 0.7;   
                        position: relative;                
                        span{
                            display: block;
                            font-size: 1.6rem;
                            margin-top: 10px;
                            &.down_arrow{
                                display: none;
                                width: 0; 
                                height: 0; 
                                margin: auto;
                                margin-top: 10px;
                                border-radius: 3px;
                                border-left: 8px solid transparent;
                                border-right: 8px solid transparent;                            
                                border-top: 8px solid $primary-color-yellow;
                            }
                            &.done_image{
                                display: none;
                                position: absolute;
                                right: -25px;
                                top: -38px;
                                img{
                                    width: 70px;
                                }
                            }
                        }
                        .svg_sec{
                            width: 70px;
                            height: 70px;                        
                            margin: auto;
                            margin-top: 0;
                            border-radius: 50%;
                            display: inline-flex;
                            align-items: center;
                            justify-content: center;
                            border: 1px $color-grey solid;
                            transition: 0.3s;
                            padding: 16px;
                            @media (max-width: $breakpoint-tablet){
                                width: 50px;
                                height: 50px;
                                padding: 10px;
                            }
                            svg{
                                width: 100%;
                            }
                        }
                        &.active, &:hover{
                            opacity: 1;
                            span{
                                &.down_arrow{
                                    display: block;
                                }
                            }
                            .svg_sec{
                                background-color: $primary-color-yellow;
                                svg{
                                    path{
                                        fill: $primary-color;
                                    }
                                }
                            }
                        }
                        &.done{
                            span{
                                &.done_image{
                                    display: block;
                                }
                            }
                        }
                    }
                    .bg_arrow{
                        display: block;
                        margin: 0 10px;
                        margin-top: 30px;
                        img{
                            width: 100%;
                        }
                    }
                }            
            }

            .mandatory_note{
                position: absolute;
                top: 20%;
                right: -7%;
                font-size: 1.2rem;
                font-style: italic;
                @media (max-width: $breakpoint-mobile-big){
                    top: auto;
                    bottom: -20px;
                }
            }

            p{
                font-size: 1.4rem;
                text-align: center;
                margin-top: 20px;
            }
        }

        .right_sec{
            width: 40%;
            background-color: #8D8D8D;
            border-radius: 5px;
            padding: 10px 25px;
            @media (max-width: $breakpoint-tablet){
                width: 50%;
            }
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }
            .bid_limit_box{
                h5{
                    font-size: 1.4rem;
                    font-weight: lighter;
                    color: $color-white;
                }

                ul{
                    display: flex;
                    justify-content: space-between;
                    color: $color-white;
                    margin-top: 50px;
                    @media (max-width: $breakpoint-mobile-big){
                        margin-top: 20px;
                        flex-wrap: wrap;
                    }
                    li{
                        @media (max-width: $breakpoint-mobile-big){
                            &:last-child{
                                margin-top: 10px;
                            }
                        }
                        span{
                            display: block;
                            font-size: 1.4rem;
                        }
                        h4{
                            font-size: 1.8rem;
                            margin-top: 10px;
                        }
                    }
                }
            }
        }
    }
}

.profile_tabbing{
    .container{    
        padding: 25px;
        box-shadow: 0px 0px 20px #0000001a;
        border-radius: 5px;
        margin-top: 50px;
        .profile_content{    
            display: none;
            &.active{
                display: block;
            }    
            .heading{
                h3{            
                    font-size: 1.6rem;
                    font-weight: lighter;
                    display: flex;
                    justify-content: space-between;
                    span{
                        font-size: 1.2rem;
                        font-style: italic;
                    }
                }
                p{
                    margin-top: 20px;
                    font-size: 1.4rem;
                }
            }
            .form_content{
                margin-top: 30px;
                .pic_upload{
                    display: flex;
                    align-items: center;
                    img{
                        width: 60px;
                    }                
                }

                ul{
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    align-items: flex-end;
                    margin-top: 30px;
                    @media (max-width: $breakpoint-mobile-big){
                        display: block;
                    }
                    li{
                        width: 30%; 
                        margin-bottom: 50px;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 100%;
                            margin-bottom: 30px;
                        }
                        &.col_6{
                            width: 48%;
                            @media (max-width: $breakpoint-mobile-big){
                                width: 100%;
                            }
                        }
                        &.col_2{
                            display: flex;
                            align-items: flex-end;
                            justify-content: space-between;
                            .custom_input{
                                width: 48%;
                            }
                            .custom_select{
                                width: 48%;
                            }
                        }

                        span{
                            &.label{
                                display: block;
                                font-size: 1.4rem !important;
                                font-weight: bold;
                            }
                        }
                        p{
                            font-size: 1.4rem;
                            font-weight: bold;
                            margin-top: 30px;
                            @media (max-width: $breakpoint-mobile-big){
                                margin-top: 15px;
                            }
                        }
                        .gender_sign{
                            display: flex;                                                
                        }

                        .user_dob{
                            display: flex;                        
                            select{
                                margin-left: 15px;
                                &:first-child{
                                    margin-left: 0;
                                }
                            }
                        }

                        &.user_password{
                            position: relative;
                            .password_sec{
                                display: flex;
                                justify-content: space-between;
                                border-bottom: 1px $primary-color solid;
                                input{
                                    padding-bottom: 10px;
                                    font-size: 1.4rem;
                                    font-family: $font-italic;
                                }
                                a{
                                    width: 20px;
                                    position: static;
                                    img{
                                        width: 100%;
                                    }
                                }
                            }
                            a{
                                font-size: 1.2rem;
                                text-decoration: underline;
                                position: absolute;
                                bottom: -20px;
                                left: 0;
                                @media (max-width: $breakpoint-mobile-big){
                                    position: static;
                                    display: inline-block;
                                    margin-top: 5px;
                                }
                            }
                        }

                        .phone_no{
                            display: flex;
                            .code{
                                width: 10%;
                                margin-right: 5px;
                                @media (max-width: $breakpoint-tablet){
                                    width: 20%;
                                }
                            }
                        }

                        .nickname_sec{
                            display: flex;
                        }

                        .custom_radio_cont{
                            margin-top: 20px;
                        }

                        .bidding_icons{
                            .image_radio{
                                margin-right: 15px;
                            }
                        }

                        .custom_select{
                            margin-top: 20px;
                        }

                        &.user_interested{
                            position: relative;                    
                            span{
                                &.label{
                                    position: static;
                                    font-style: normal;
                                }
                                position: absolute;
                                left: 0;
                                bottom: -20px;
                                font-size: 1.2rem;
                                font-style: italic;
                            }
                        }

                        &.upload_sec{                            
                            .fileUpload{
                                &::before{
                                    content: "Browse";
                                }
                            }
                        }
                    }
                }

                .save_btn{
                    margin-top: 20px;
                }
            }
        }
    }
    
}

.purchase_search{
    margin-top: 50px;
    .container{
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        @media (max-width: $breakpoint-mobile-big){
            display: block;
        }
        .left_sec{
            width: 45%;
            border-bottom: 1px $color-dark-grey solid;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
            }
            input[type=text]{
                font-size: 1.4rem;
                font-style: italic;
                display: inline-block;
                width: 100%;
                padding-top: 5px;
                padding-bottom: 10px;
            }
            a{
                display: inline-block;
                width: 20px;
                margin-left: 10px;
                margin-right: 15px;
                img{
                    width: 100%;
                }
            }
        }

        .right_sec{
            display: flex;
            align-items: center;  
            width: 25%;     
            @media (max-width: $breakpoint-mobile-big){
                width: 80%;
                margin-top: 20px;
            }     
            span{
                display: inline-block;
                font-size: 1.4rem;
                font-weight: bold;
                width: 50%;
                margin-right: 15px;
                text-align: right;
                @media (max-width: $breakpoint-mobile-big){
                    width: 30%;
                    text-align: left;
                }
            }
            select{
                width: 50%;
                @media (max-width: $breakpoint-mobile-big){
                    width: 70%;
                }
            }
        }
    }
}

.purchase_order{
    margin-top: 30px;
    .purchase_box{
        margin-top: 30px;
        box-shadow: 0px 0px 20px #0000001a;
        border-radius: 5px;
        padding: 20px;
        display: flex;
        justify-content: space-between;
        @media (max-width: $breakpoint-mobile-big){
            display: block;
        }
        .purchase_img{
            width: 15%;
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                text-align: center;
            }
            img{
                max-width: 100%;
            }
        }

        .purchase_info{
            width: 83%;
            display: flex;
            justify-content: space-between; 
            @media (max-width: $breakpoint-mobile-big){
                width: 100%;
                display: block;
            }       
            .left_sec{
                display: flex;
                flex-flow: column;
                justify-content: space-between;
                padding: 15px 0;
                @media (max-width: $breakpoint-mobile-big){
                    flex-flow: row;
                }
                div{                    
                    h5{
                        font-size: 1.4rem;                    
                    }
                    h6{
                        font-size: 1.4rem;
                        font-weight: lighter;
                    }
                    span{
                        display: block;
                        font-size: 1.4rem;
                        margin-top: 5px;
                        font-weight: lighter;
                    }

                }
            }
            .right_sec{
                display: flex;
                flex-flow: column;
                justify-content: space-between;
                padding: 15px 0;
                @media (max-width: $breakpoint-mobile-big){                    
                    flex-flow: row;
                    align-items: flex-end;
                    padding: 0;
                }
                div{
                    text-align: right;
                    @media (max-width: $breakpoint-mobile-big){
                        text-align: left;
                    }
                    h4{
                        font-size: 1.6rem; 
                        @media (max-width: $breakpoint-tablet){
                            margin-top: 15px;
                        }                   
                    }
                    span{
                        display: block; 
                        font-size: 1.4rem;     
                        margin-top: 5px;              
                        &.green_ball{                    
                            display: inline-block;
                            width: 30px;
                            height: 30px;
                            border-radius: 50%;
                            background-color: $color-dark-green;
                        }
                        &.brown_ball{                    
                            display: inline-block;
                            width: 30px;
                            height: 30px;
                            border-radius: 50%;
                            background-color: $color-brown;
                        }
                    }
                }
            }
        }
    }
}

.small_tabs{
    margin-top: 50px;
    .container{
        border-bottom: 1px $color-grey solid;
        ul{
            display: flex;
            overflow-x: auto;
            li{
                @media (max-width: $breakpoint-mobile-big){
                    flex-grow: 1;
                    flex-shrink: 0;
                }
                a{
                    display: block;
                    font-size: 1.6rem;
                    padding: 0px 50px 15px;
                    position: relative;
                    transition: 0.3s;
                    @media (max-width: $breakpoint-mobile-big){
                        padding: 0 20px 15px;
                        text-align: center;
                    }
                    &.active, &:hover{
                        font-weight: bold;
                        &::after{
                            content: "";
                            width: 100%;
                            height: 2px;
                            background-color: $primary-color-yellow;
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            right: 0;                        
                        }
                    }                                    
                }
            }
        }
    }
}

.gallery_tab_inner{
    display: none;
    &.active{
        display: block;
    }
    .gallery_tab_content{
        margin-top: 30px;
        .container{
            .view_auction{
                display: flex;
                align-items: center;
                justify-content: space-between;
                @media (max-width: $breakpoint-mobile-big){
                    display: block;
                }
                .left_sec{
                    display: flex;
                    align-items: center;
                    width: 35%;
                    @media (max-width: $breakpoint-tablet){
                        width: 50%;
                    }
                    @media (max-width: $breakpoint-mobile-big){
                        width: 100%;
                    }
                    span{
                        display: inline-block;
                        font-size: 1.4rem;
                        font-weight: bold;
                        width: 25%;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 30%;
                        }
                    }
                    select{
                        width: 60%;
                        margin-top: 5px;
                        @media (max-width: $breakpoint-mobile-big){
                            width: 65%;
                        }
                    }
                }

                .right_sec{
                    @media (max-width: $breakpoint-mobile-big){
                        text-align: right;
                    }
                    a{
                        display: inline-block;
                        font-size: 1.4rem;                    
                        text-decoration: underline;
                        transition: 0.3s;
                        &:hover{
                            color: $primary-color-yellow;
                        }
                        @media (max-width: $breakpoint-mobile-big){
                            margin-top: 15px;
                        }
                    }
                }
            }

            .gallery_price{
                &.price_dropdown{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    background-color: $color-grey;
                    padding: 15px;
                    border-radius: 5px;
                    margin-top: 40px;
                    span{
                        font-size: 1.6rem;
                        font-weight: bold;
                        font-style: italic;
                    }
                }
            }
        }
    }
}